<template>
  <div>
    <div class="cart">
      <!--头部，返回首页和返回上一级页面-->
      <div class="home clearfix">
        <a href="/">返回首页</a>
        <a @click="previousPage">返回上一页</a>
      </div>

      <!--当购物车有商品的时候-->
      <el-row v-if="carts.length>0">
        <el-row>
          <el-col>
            <el-table ref="multipleTable" :data="carts" tooltip-effect="dark" style="width: 100%"
                      @selection-change="handleSelectionChange">

              <el-table-column type="selection" width="55"></el-table-column>

              <el-table-column prop="book_name" label="商品名称" width="400">
                <template slot-scope="scope">
                  <a class="name-a" @click="bookInfo(scope.row.book_id)">
                    <img alt="" :src="scope.row.book_img" style="height: 60px;width: 60px"/>
                    {{ scope.row.book_name }}
                  </a>
                </template>
              </el-table-column>

              <el-table-column prop="price" label="单价(元)" show-overflow-tooltip></el-table-column>

              <el-table-column
                fixed="right" label="数量" width="120">
                <template slot-scope="scope">
                  <el-input type="number" :min="1" @change="buyNumChange(scope.row)"
                            v-model="scope.row.num"></el-input>
                </template>
              </el-table-column>

              <el-table-column prop="price" label="小计(元)" show-overflow-tooltip>
                <template slot-scope="scope">
                    {{scope.row.price*scope.row.num}}
                </template>
              </el-table-column>

              <el-table-column
                fixed="right" label="操作" width="120">
                <template slot-scope="scope">
                  <el-button type="danger" size="small" @click="deleteRow(scope.row.cart_id)">删除
                  </el-button>
                </template>
              </el-table-column>

            </el-table>
          </el-col>
        </el-row>

        <!--结算部分-->
        <el-row>
          <el-col class="pay-nav">
            <el-col :span="4">
              <el-button class="clear-all-cart-btn" type="danger" @click="harvestInfoVisible1 = true">清空购物车
              </el-button>
              &nbsp;<!--<a href="/">继续购物</a>-->
              <el-divider direction="vertical"></el-divider>
            </el-col>
            <el-col :span="8" style="color: #757575;">
              共<span style="color: #ff6700;">{{ total }}</span>件商品，
              已选中<span style="color: #ff6700;">{{ checkNum }}</span>件
            </el-col>
            <el-col :span="8" style="text-align: right;color: #ff6700;">
              合计： <span class="all-money">{{ allMoney }}元</span>
            </el-col>
            <el-col :span="4" style="text-align: right;">
              <el-button class="pay-btn" @click="settlementBox">结算</el-button>
            </el-col>
          </el-col>
        </el-row>
      </el-row>

      <!--当购物车没有商品的时候-->
      <div v-if="carts.length===0">
        <div class="tips-words">
          <i></i>
          <div>
            <h3>您的购物车空空如也！<a href="/">去逛逛</a></h3>
            <dl>
              <dt>建议您:</dt>
              <dd>1.看看您感兴趣的图书</dd>
              <dd>2.告诉我们您想找的商品，我们马上进货！</dd>
            </dl>
          </div>
        </div>
        <HomeFooter></HomeFooter>
      </div>

      <!--结算填写地址信息部分-->
      <el-dialog title="收货信息" :visible.sync="harvestInfoVisible" width="30%" @close="harvestInfoClose">
        <el-form :model="settlement" :rules="harvestInfoRrules" ref="harvestInfoRuleForm">
          <el-form-item label="收货人姓名" prop="nick_name">
            <el-input v-model="settlement.nick_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="收货人电话" prop="tel">
            <el-input v-model="settlement.tel" autocomplete="off" type="number"></el-input>
          </el-form-item>
          <el-form-item label="收货地址" prop="address">
            <el-input v-model="settlement.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="harvestInfoVisible = false">取 消</el-button>
          <el-button type="primary" @click="receiptInfo">确 定</el-button>
        </div>
      </el-dialog>

      <!--清空购物车填写地址信息部分-->
      <el-dialog title="收货信息" :visible.sync="harvestInfoVisible1" width="30%" @close="harvestInfoClose1">
        <el-form :model="settlement" :rules="harvestInfoRrules" ref="harvestInfoRuleForm1">
          <el-form-item label="收货人姓名" prop="nick_name">
            <el-input v-model="settlement.nick_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="收货人电话" prop="tel">
            <el-input v-model="settlement.tel" autocomplete="off" type="number"></el-input>
          </el-form-item>
          <el-form-item label="收货地址" prop="address">
            <el-input v-model="settlement.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="harvestInfoVisible1 = false">取 消</el-button>
          <el-button type="primary" @click="emptyCart">确 定</el-button>
        </div>
      </el-dialog>

      <!--分页区-->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 3, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

      <HomeFooter></HomeFooter>
    </div>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  name: "Cart",
  data(){
    //验证手机号的规则
    var checkMobile = (rule, value, cd) => {
      //验证手机号的正则表达式
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //合法电话
        return cd()
      }
      cd(new Error('请输入正确的电话号'))
    }
    return{
      //用户
      user: {},
      //提交到后台的数据
      queryInfo: {
        user_id: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5,
      },
      //购物车总条数
      total: 0,
      //购物车信息
      carts: [],
      //购物车所有商品价格总和
      allMoney: 0,
      //选中的数量
      checkNum: 0,
      //收货信息
      settlement: {
        //选中的商品
        multipleSelection: [],
        //收货人姓名
        nick_name: '',
        //收货人电话
        tel: 0,
        //收获地址
        address: '',
      },
      //控制收货信息对话框显示与隐藏
      harvestInfoVisible: false,
      //收获信息的验证规则
      harvestInfoRrules: {
        nick_name: [
          {required: true, message: '请输入收货人姓名', trigger: 'blur'},
          {min: 2, max: 5, message: '长度在 2 到 4 个字符', trigger: 'blur'}
        ],
        tel: [
          {
            required: true,//必填
            message: '请输入电话号码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkMobile,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        address: [
          {
            required: true,//必填
            message: '请输入地址',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {min: 8, max: 18, message: '长度在 8 到 18 个字符', trigger: 'blur'}
        ]
      },
      //控制清空购物车收货信息对话框显示与隐藏
      harvestInfoVisible1: false,
    }
  },
  created() {
    //从sessionStorage取出已经登录的用户
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    this.queryInfo.user_id = this.user.user_id
    //根据用户id查询用户的购物车信息
    this.getCartByUserId()
    //将用户的地址，电话，姓名放到收货对话框中，以便用户修改
    this.settlement.address=this.user.address
    this.settlement.tel=this.user.phone
    this.settlement.nick_name=this.user.username
  },
  methods:{
    //根据用户id查询用户的购物车信息
    getCartByUserId() {
      this.$http.get('/user/getCartByUserId',{params:this.queryInfo}).then(res=>{
        if (res.data.code !== 200) {
          return this.$message.error('获取购物车信息失败')
        }
        this.carts = res.data.data.info.list
        this.total = res.data.data.info.total
        //总价格
        this.allMoney = res.data.data.totalprice.toFixed(2)
      })
    },
    //获取选中数据的值
    handleSelectionChange(val) {
      this.settlement.multipleSelection = val;
      this.checkNum = this.settlement.multipleSelection.length;
    },
    //将图书id通过路由地址携带到另一个组件中查询图书的详情
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //获取改变之后的购买数量的值，使用自带的change事件，会默认传入修改之后的值
    buyNumChange(item) {
      if (item.num < 1) {
        this.$message.error("最少要添加1件")
        item.num = 1
      }

      //查询这本图书的库存，如果库存不足就不能修改购物的数量
      this.$http.get('/public/getBookDetails?book_id=' + item.book_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('查询图书库存失败')
        }
        if (res.data.data.book.total < item.num) {
          item.num = res.data.data.book.total
          return this.$message.error('对不起,库存不足,当前库存只有' + res.data.data.book.total + '件')
        }
        //发送请求修改购物车添加数量
        this.$http.get('/user/buyNumChange',{
          params:{
            cart_id: item.cart_id,
            num: item.num
          }
        }).then(res=>{
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.error('修改商品数量失败')
          }
          this.getCartByUserId()
        })
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCartByUserId()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCartByUserId()
    },
    //根据购物车id删除信息
    async deleteRow(cart_id) {
      const confirmResult = await this.$confirm('是否删除该商品信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/user/deleteRow?cart_id=' + cart_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除商品失败')
        }
        this.$message.success('删除商品成功')
        this.getCartByUserId()
      })
    },
    //重置表单验证
    harvestInfoClose() {
      this.$refs.harvestInfoRuleForm.resetFields()
    },
    //重置表单验证
    harvestInfoClose1() {
      this.$refs.harvestInfoRuleForm1.resetFields()
    },
    //显示收获信息对话框，并提示用户最少结算一件商品
    settlementBox() {
      if (this.settlement.multipleSelection.length <= 0) {
        return this.$message.info('请选择结算的商品')
      }
      this.harvestInfoVisible = true
    },
    //结算
    receiptInfo() {
      this.$refs.harvestInfoRuleForm.validate(valid => {
        if (!valid) return

        var carts = JSON.stringify(this.settlement.multipleSelection)
        this.$http.get('/user/receiptInfo', {
          params: {
            carts: carts,
            nick_name:this.settlement.nick_name,
            tel:this.settlement.tel,
            address:this.settlement.address
          }
        }).then(res => {
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.info('结算商品失败')
          }
          this.$router.go(0)
          this.getCartByUserId()
          this.$message.success('结算商品成功')
          this.harvestInfoVisible = false
        })

      })
    },
    //清空购物车
    async emptyCart() {
      const confirmResult = await this.$confirm('是否清空购物车?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.get('/user/emptyCart', {
        params: {
          user_id: this.queryInfo.user_id,
          nick_name: this.settlement.nick_name,
          tel: this.settlement.tel,
          address: this.settlement.address
        }
      }).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('清空购物车失败')
        }
        this.$router.go(0)
        this.getCartByUserId()
        this.$message.success('结算商品成功')
        this.harvestInfoVisible1 = false
      })
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.el-pagination{
  text-align: center;
  margin-top:15px ;
}
.tips-words{
  width: 1200px;
  height: 300px;
  margin: 0 auto;
  margin-top: 10px;
  background-color: #FFF5ED;
}

.tips-words i{
  width: 54px;
  height: 68px;
  margin: 116px 0 116px 60px;
  background-position: -239px -152px;
  display: block;
  float: left;
  padding:0 20px 0 0;
  background: url("https://res.suning.cn/project/search/searchpage_new/css/images/pic.png?20210126") -165px -153px no-repeat;;
}

.tips-words div{
  float: left;
  margin-top: 114px;
}

.tips-words div h3 a{
  color: #28e;
}

.tips-words div h3{
  color: #666666;
}

.tips-words div dl{
  color: #666666;
  font-size: 12px;
}

.tips-words div dl dd em a{
  color: #666666;
  font-style: normal;
}

.cart{
  width: 1200px;
  margin: 10px auto 0;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}


.no-cart > h2 {
  margin: 70px 0 0 0;
  font-size: 36px;
  color: #b0b0b0;
}

.no-cart > a {
  text-decoration: none;
  color: white;
  background-color: #ff6700;
  display: inline-block;
  margin: 30px;
  padding: 20px;
  width: 130px;
  text-align: center;
  border-radius: 20px;
  font-size: 20px;
}

a:hover {
  cursor: pointer;
}

.my-cart a {
  text-decoration: none;
  color: #565656;
  font-size: 20px;
}

.all-money {
  font-size: 25px;
  color: #ff6700;
  font-weight: bold;
}

.pay-btn {
  color: white;
  background-color: #ff6700;
  border-radius: 0;
  border: 0;
  height: 50px;
  width: 160px;
  font-size: 20px;
}

.clear-all-cart-btn {
  border-radius: 0;
  border: 0;
  height: 50px;
  width: 160px;
  font-size: 20px;
}

.pay-nav {
  margin-top: 20px;
  background-color: white;
  height: 50px;
  line-height: 50px;
}

.pay-nav a {
  text-decoration: none;
  color: #757575;
}

.check-item img {
  width: 70px;
  height: 70px;
  margin-top: 10px;
}

.name-a {
  text-decoration: none;
  color: black;
}

.name-a > img {
  float: left;
}

.book-item > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.book-item img {
  display: block;
  margin: 20px auto;
  width: 220px;
  height: 220px;
}


hr {
  width: 350px;
  display: inline-block;
  margin: 0 20px 10px 20px;
}

.main-title > h2 {
  color: #848484;
  text-align: center;
  display: inline-block;
}

.main-title > img {
  display: inline-block;
}
</style>
