<script>
import HomeFooter from "./HomeFooter";
export default {
  name: "HomeSubject",
  data() {
    return {
      //轮播图
      tupian: [],
      //随机查询出来的8条数据
      randomBookList: [],
      //随机获取13的条类别信息
      thirteenCategory: [],
      //新书上架书籍列表
      newBook: [],
      //获取首页热销图书列表
      bookSales: [],
      //随机查询10条数据
      randomBookList_two:[]
    }
  },
  created() {
    //获取轮播图
    this.getRotationChartList()
    //随机查询8本图书，每日精选区
    this.randomBook()
    //随机获取13条类别信息
    this.getThirteenCategory();
    //获取最新上架的图书
    this.getNewBook()
    //获取首页热销图书
    this.gethomesalesRanking()
    //随机查询10本图书，猜你喜欢区
    this.randomBook_two()
  },
  methods: {
    //获取轮播图
    getRotationChartList() {
      this.$http.get("/public/getRotationChartList").then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("获取轮播图失败")
        }
        this.tupian = res.data.data
      })
    },
    //随机查询8本图书
    randomBook() {
      this.$http.get('/public/randomBook').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("随机查询8条图书信息失败")
        }
        this.randomBookList = res.data.data
      })
    },
    //随机获取13条类别信息
    getThirteenCategory() {
      this.$http.get('/public/getThirteenCategory').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("获取13条类别失败")
        }
        this.thirteenCategory = res.data.data
      })
    },
    //获取最新上架的图书
    getNewBook() {
      this.$http.get('/public/getNewBook').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("获取最新上架图书失败")
        }
        this.newBook = res.data.data
      })
    },
    //获取首页热销图书
    gethomesalesRanking(){
      this.$http.get('/public/salesRanking').then(res=>{
        if (res.data.code!==200){
          return this.$message.error('获取热销图书失败')
        }
        this.bookSales=this.getList(res.data.data)
      })
    },
    //热销图书数据处理
    getList(Array) {
      let item = []
      let items = []
      for (let Arrays of Array) {
        item.push(Arrays)
        if (item.length == 5) {
          items.push(item)
          item = []
        }
      }
      if (item.length > 0) {
        items.push(item)
      }
      return items
    },
    //随机查询10本图书，猜你喜欢区
    randomBook_two(){
      this.$http.get('/public/randomBook_two').then(res=>{
        if (res.data.code!==200){
          return this.$message.error("获取猜你喜欢区失败")
        }
        this.randomBookList_two=res.data.data
      })
    },
    //去类别图书页面，将类别的id通过路由地址携带到另一个组件中进行查询
    goCategoryBookList(category_id,category_name){
      this.$router.push('/categorybooklist?category_id='+category_id+"&category_name="+category_name)
      //刷新整个页面
      this.$router.go(0)
    },
    //将图书id通过路由地址携带到另一个组件中查询图书的详情
    goBookInfo(book_id){
      this.$router.push('/bookinfo?book_id='+book_id)
    },
    //去更多图书分类页
    gomorebookcategory(){
      this.$router.push('/morebookcategory')
    }
  },
  components:{
    HomeFooter
  }
}
</script>

