<template>
  <div class="collection">
    <!--头部，返回首页和返回上一级页面-->
    <div class="home clearfix">
      <a href="/">返回首页</a>
      <a @click="previousPage">返回上一页</a>
    </div>
    <!--  是当用户没有收藏图书的时候  -->
    <div class="load-state" v-if="collectionList.length===0">
        <p class="load-pic">
          <img src="../../assets/images/load-kong.png" alt="">
        </p>
        <p>嗷~这里木有收藏~</p>
        <p class="load-open">
          推荐您去：<a href="/">首页</a>逛逛
        </p>
    </div>

    <!--当用户有收藏图书的时候-->
    <ul class="clearfix" v-if="collectionList.length!=0">
      <li v-for="item in collectionList" :key="item.book_id">
        <img :src="item.book_img" @click="bookInfo(item.book_id)">
        <h4>{{item.book_name}}</h4>
        <div class="info">
            <span>
              收藏时间：{{item.collection_time|dateFormat1}}
            </span>
        </div>
        <el-button type="danger" size="mini" class="cancelbutton" @click="deleteCollection(item.id)">取消收藏</el-button>
      </li>
    </ul>

    <!--分页区-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total" v-if="collectionList.length!=0">
    </el-pagination>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    return {
      //用户
      user: {},
      //查询信息
      queryInfo: {
        //用户id
        user_id: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5,
      },
      //用户收藏列表
      collectionList: [],
      //总条数
      total: 0
    }
  },
  created() {
    //从sessionStorage取出已经登录的用户
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //获取用户的收藏列表
    this.getCollectionList()
  },
  methods: {
    //获取用户的收藏列表
    getCollectionList() {
      this.queryInfo.user_id = this.user.user_id
      this.$http.get('/user/getCollectionList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取用户收藏列表失败')
        }
        this.collectionList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //将图书id通过路由地址携带到另一个组件中查询图书的详情
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //删除收藏
    async deleteCollection(id) {
      const confirmResult = await this.$confirm('是否取消收藏？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消')
      }

      this.$http.get('/user/deleteCollectionById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('取消失败')
        }
        this.$message.success('取消成功')
        this.getCollectionList()
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCollectionList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCollectionList()
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.load-state .load-open a{
  color: #28e;
}

.load-state .load-open{
  font-size: 14px;
  font-weight: 400;
}

.load-state{
  padding: 100px;
  text-align: center;
  font-weight: 700;
}

.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}
.collection{
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.el-pagination {
  text-align: center;
}



.collection ul {
  width: 1200px;
  margin-top: 10px;
}

.collection ul li {
  float: left;
  width: 225px;
  height: 310px;
  background-color: #FFFFFF;
  margin-right: 8px;
  margin-bottom: 15px;
  border: 1px solid #f3f3f3;
}

.collection ul li:hover {
  cursor:pointer;
  z-index: 2; /*设置在顶层显示*/
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1); /*添加阴影*/
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0); /*向上浮动*/
  transform: translate3d(0, -2px, 0);
}

.collection ul li > img {
  width: 150px;
  height: 150px;
  margin: 20px 0 10px 35px;
}

.collection ul li h4 {
  height: 46px;
  padding: 10px 20px 0;
  font-size: 12px;
  font-weight: 400;
  /*超出的部分隐藏*/
  overflow: hidden;
  /*超出的部分使用省略号表示*/
  text-overflow: ellipsis;
  /* 弹性伸缩盒子模型显示 */
  display: -webkit-box;
  /* 限制在一个块元素显示的文本的行数 */
  -webkit-line-clamp: 3;
  /* 设置或检索伸缩盒对象的子元素的排列方式 */
  -webkit-box-orient: vertical;
}

.collection ul li h4:hover {
  color: #ff7c2d;
}

.collection ul li .info {
  margin: 15px 20px 0 25px;
}

.collection ul li .info span {
  font-size: 12px;
  color: #666;
}

.collection ul li .cancelbutton{
  float: right;
  margin-top: 5px;
  margin-right: 20px;
}
</style>
