<!--轮播图管理-->
<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>首页管理</el-breadcrumb-item>
      <el-breadcrumb-item>首页轮播图管理</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-alert title="温馨提示:首页轮播图只能放五张图片" type="success" center show-icon>
      </el-alert>

      <el-upload class="upload-demo" accept="image/jpg" with-credentials
                 :action="uploadURL" multiple name="rotationChartImg"
                 :on-success="fileUpload" :disabled="rotationChartlistLenth">
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>

      <!--轮播图列表区  border是竖线，:stripe是隔行变色-->
      <el-table :data="rotationChartlist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="图片使用位置">
          <template>
            首页轮播图
          </template>
        </el-table-column>
        <el-table-column label="轮播图图片">
          <template slot-scope="scope">
            <el-image style="width: 280px; height: 100px"
                      :src="scope.row.img"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="img_name" label="图片名称"></el-table-column>
        <el-table-column prop="img" label="图片地址"></el-table-column>
        <el-table-column label="操作" width="150px">
          <template slot-scope="scope">
            <!--删除按钮-->
            <el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeRotationChartById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //轮播图列表
      rotationChartlist: [],
      //上传图片的URL地址
      /*uploadURL: 'http://localhost:8080/bookstore_SSM_Vue/admin/uploadRotationChart',*/
      uploadURL: 'http://localhost:8080/admin/uploadRotationChart',
    }
  },
  created() {
    //获取所有轮播图
    this.getRotationChartList()
  },
  methods: {
    //获取所有轮播图
    getRotationChartList() {
      this.$http.get('/admin/getRotationChartList').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取轮播图列表失败')
        }
        this.rotationChartlist = res.data.data
      })
    },
    //文件上传成功会触发这个函数，参数：rotationChartImg是文件上传组件中action属性请求服务器后返回来的值
    fileUpload(rotationChartImg) {
      console.log(rotationChartImg.filename)
      console.log(rotationChartImg.upload)
      //添加轮播图图片到数据库中
      this.$http.post('/admin/addRotationChart', {
          img: rotationChartImg.upload,
          img_name: rotationChartImg.filename
        }
      ).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('上传轮播图失败')
        }
        this.$message.success('上传轮播图成功')
        this.getRotationChartList()
      })
    },
    //删除轮播图
    async removeRotationChartById(id) {
      const confirmResult = await this.$confirm('是否删除此轮播图图片?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.post('/admin/removeRotationChartById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除轮播图失败')
        }
        this.$message.success('删除轮播图成功')
        this.getRotationChartList()
      })

    }
  },
  //计算属性
  computed: {
    rotationChartlistLenth() {
      if (this.rotationChartlist.length == 5) {
        return true;
      }
      return false;
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-table {
  margin-top: 30px;
}

.upload-demo {
  margin-top: 10px;
}
</style>
