<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <!--搜索与添加区域  el-row行，el-col列-->
      <el-row :gutter="20">
        <el-col :span="5">
          <el-input placeholder="输入图书关键字" v-model="queryInfo.query" clearable @clear="getBooklist">
            <el-button slot="append" icon="el-icon-search" @click="getBooklist"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="goAddBook">添加图书</el-button>
        </el-col>
      </el-row>
      <el-table :data="booklist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="book_img" label="书籍封面" width="120px">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.book_img"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="book_name" label="书名"></el-table-column>
        <el-table-column prop="author" label="作者"></el-table-column>
        <el-table-column prop="book_man" label="出版社"></el-table-column>
        <el-table-column prop="price" label="价格" width="50px"></el-table-column>
        <el-table-column prop="introduction" label="介绍">
          <template slot-scope="scope">
            <div class="introduction">
              {{scope.row.introduction}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="total" label="库存" width="100px"></el-table-column>
        <el-table-column prop="sales" label="销量" width="100px"></el-table-column>
        <el-table-column prop="add_time" label="进库时间" width="140px">
          <template slot-scope="scope">
            <div style="font-size: 10px">
              {{ scope.row.add_time|dateFormat1 }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="250px">
          <template slot-scope="scope">
            <!--修改按钮-->
            <el-button type="success" @click="getBookDetails(scope.row.book_id)">编辑</el-button>
            <!--删除按钮-->
            <el-button type="danger" @click="removeBookById(scope.row.book_id)">删除</el-button>

          </template>
        </el-table-column>
      </el-table>

      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>

      <!--修改图书详情对话框区-->
      <el-dialog title="修改图书信息" :visible.sync="editDialogVisible" @close="editDialogClosed">
        <el-form :model="book" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="right"
                 :inline="true">
          <el-table-column label="图书基本信息" name="0">
            <el-form-item label="书名" prop="book_name">
              <el-input v-model="book.book_name"></el-input>
            </el-form-item>
            <el-form-item label="作者" prop="author">
              <el-input v-model="book.author"></el-input>
            </el-form-item>
            <el-form-item label="出版社" prop="book_man">
              <el-input v-model="book.book_man"></el-input>
            </el-form-item>
            <el-form-item label="单价" prop="price">
              <el-input v-model="book.price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="库存" prop="total">
              <el-input v-model="book.total" type="number"></el-input>
            </el-form-item>
            <el-form-item label="进库的时间" prop="add_time">
              <el-date-picker v-model="book.add_time" type="datetime" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
            <el-form-item label="销量">
              <el-input v-model="book.sales" disabled></el-input>
            </el-form-item>
            <el-form-item label="图书类别" prop="add_time">
              <el-select v-model="book.category_id" placeholder="请选择类别" filterable>
                <el-option v-for="item in categoryList" :key="item.id" :label="item.category_name"
                           :value="item.category_id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="图书介绍" prop="introduction" style="width: 500px">
              <el-input type="textarea" v-model="book.introduction" style="width: 400px;"></el-input>
            </el-form-item>
            <el-form-item label="图片地址" prop="book_img">
              <el-input v-model="book.book_img" style="width: 400px;"></el-input>
            </el-form-item>
          </el-table-column>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editBookInfo">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //获取所有图书列表参数对象
      queryInfo: {
        query: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 10,
      },
      //所有书籍列表
      booklist: [],
      //图书总条数
      total: 0,
      //图书
      book: {},
      //所有类别
      categoryList: [],
      //控制修改图书对话框显示与隐藏
      editDialogVisible: false,
      //修改表单的验证规则对象
      editFormRules: {
        book_name: [
          {
            required: true,//必填
            message: '请输入图书名称',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 15,
            message: '用户名的长度在2~15个字符之间',
            trigger: 'blur'
          }
        ],
        author: [
          {
            required: true,//必填
            message: '请输入作者姓名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 6,
            message: '用户名的长度在2~6个字符之间',
            trigger: 'blur'
          }
        ],
        book_man: [
          {
            required: true,//必填
            message: '请输入出版社',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            message: '出版社最少不能低于两个字符',
            trigger: 'blur'
          }
        ],
        price: [
          {
            required: true,//必填
            message: '请输入单价',
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        total: [
          {
            required: true,//必填
            message: '请输入库存',
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        introduction: [
          {
            required: true,//必填
            message: '请输入图书介绍',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            message: '图书介绍最少不能低于两个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //获取所有图书
    this.getBooklist()
  },
  methods: {
    //获取所有图书的数据
    async getBooklist() {
      this.queryInfo.query=this.queryInfo.query.trim()
      await this.$http.get('/public/getBookList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取所有书籍信息失败')
        }
        this.booklist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getBooklist()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getBooklist()
    },
    //添加图书
    goAddBook() {
      this.$router.push('/adminbook/goAddBook')
    },
    //根据id获取图书详情，将数据放到修改修改图书对话框中
    getBookDetails(id) {
      this.$http.get('/admin/getBookDetails?book_id=' + id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('查询图书详情失败')
        }
        this.book = res.data.data.book
        this.categoryList = res.data.data.categoryList
        this.editDialogVisible = true
      })
    },
    //根据图书id删除图书
    async removeBookById(id) {
      const confirmResult = await this.$confirm('此操作将永久该图书, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err=>err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.post('/admin/removeBookById?book_id='+id).then(res=>{
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('删除图书失败')
        }
        this.$message.success('删除图书成功')
        this.queryInfo.query=''
        this.getBooklist()
      })
    },
    //监听修改用户对话框的关闭事件，重置表单验证
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //修改图书信息
    editBookInfo(){
        this.$http.post('/admin/editBookInfo',this.book,{emulateJSON: true}).then(res=>{
          if (res.data.code!==200||res.data.data<=0){
            return this.$message.error('修改图书信息失败')
          }
          this.$message.success('修改图书信息成功')
          this.editDialogVisible=false
          this.getBooklist()
        })
    }
  },
}
</script>

<style scoped>
.introduction{
  overflow: hidden;
  text-overflow: ellipsis;
  /* 弹性伸缩盒子模型显示 */
  display: -webkit-box;
  /* 限制在一个块元素显示的文本的行数 */
  -webkit-line-clamp: 5;
  /* 设置或检索伸缩盒对象的子元素的排列方式 */
  -webkit-box-orient: vertical;
}

.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
