<!--类别管理-->
<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>分类管理</el-breadcrumb-item>
      <el-breadcrumb-item>图书类别</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <!--搜索与添加区域  el-row行，el-col列-->
      <el-row :gutter="20">
        <el-col :span="5">
          <el-input placeholder="输入类别关键字" v-model="queryInfo.query" clearable @clear="getCategoryList">
            <el-button slot="append" icon="el-icon-search" @click="getCategoryList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-input placeholder="新增分类名称" v-model="addCategoryName" class="addcategoryinput">
          </el-input>
          <el-button type="primary" @click="addCategory">新增</el-button>
        </el-col>
      </el-row>
      <el-table :data="categoryList" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="category_name" label="类别名称"></el-table-column>
        <el-table-column label="操作" width="250px">
          <template slot-scope="scope">
            <!--修改按钮-->
            <el-button type="success" @click="queryCategoryById(scope.row.category_id)">编辑</el-button>
            <!--删除按钮-->
            <el-button type="danger" @click="removeCategory(scope.row.category_id)">删除</el-button>

          </template>
        </el-table-column>
      </el-table>

      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>


      <!--修改用类别对话框-->
      <el-dialog title="修改类别" :visible.sync="categoryVisible" width="30%" @close="categoryClosed">
        <el-form :model="category" :rules="categoryFormRules" ref="categoryFormRef" label-width="100px">
          <el-form-item label="类别id">
            <el-input v-model="category.category_id" disabled></el-input>
          </el-form-item>
          <el-form-item label="类别名称" prop="category_name">
            <el-input v-model="category.category_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="categoryVisible = false">取 消</el-button>
          <el-button type="primary" @click="updataCategory">确 定</el-button>
        </span>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //所有图书类别
      categoryList: [],
      //查询的条件
      queryInfo: {
        query: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 10
      },
      //总条数
      total: 0,
      //添加类别的名称
      addCategoryName: '',
      //控制修改类别对话框显示与隐藏
      categoryVisible: false,
      //对话框类别信息
      category: {},
      //修改表单的验证规则对象
      categoryFormRules: {
        category_name: [
          {
            required: true,//必填
            message: '请输入类别名称',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 6,
            message: '类别名称的长度在2~6个字符之间',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //获取所有图书类别
    this.getCategoryList()
  },
  methods: {
    //获取所有图书类别
    getCategoryList() {
      this.queryInfo.query = this.queryInfo.query.trim()
      this.$http.get('/admin/getCategoryList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取所有类别失败')
        }
        this.categoryList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCategoryList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCategoryList()
    },
    //添加类别
    addCategory() {
      if (this.addCategoryName === '') {
        return this.$message.info('请输入分类名称')
      }
      this.$http.post('/admin/addCategory?category_name=' + this.addCategoryName).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('添加分类失败')
        }
        this.$message.success('添加分类成功')
        this.addCategoryName = ''
        this.getCategoryList()
      })
    },
    //监听修改用户对话框的关闭事件，重置表单验证
    categoryClosed() {
      this.$refs.categoryFormRef.resetFields()
    },
    //根据类别id查询类别
    queryCategoryById(id) {
      this.$http.get('/admin/queryCategoryById?id=' + id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('查询类别失败')
        }
        this.category = res.data.data
        this.categoryVisible = true
      })
    },
    //修改类别
    updataCategory() {
      this.$http.post('/admin/updataCategory', this.category, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('修改失败')
        }
        this.$message.info('修改成功')
        this.categoryVisible = false
        this.getCategoryList()
      })
    },
    //删除类别
    async removeCategory(id) {
      const confirmResult = await this.$confirm('确认删除类别?请确认该类别下没有图书，否则将删除失败!', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)
      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.post('/admin/removeCategory?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data === 'removeNo2') {
          return this.$message.error('删除失败')
        }
        if (res.data.data === 'removeNo1') {
          this.$message.info('此类别下已有图书,无法删除')
        }
        if (res.data.data === 'removeYes') {
          this.$message.success('删除成功')
          this.getCategoryList()
        }

      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.addcategoryinput {
  width: 60%;
}

.el-table {
  margin-top: 10px;
}
</style>
