<template>
  <div>
    <!--所有图书列表以及用户模糊查询到的图书-->
    <div class="booklist clearfix" v-if="bookCategoryByIdList.length!==0">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>{{category_name}}</el-breadcrumb-item>
      </el-breadcrumb>
      <a href="/">返回首页</a>
      <ul class="clearfix">
        <li v-for="item in bookCategoryByIdList" :key="item.book_id" @click="goBookInfo(item.book_id)">
          <img :src="item.book_img">
          <h4>{{item.book_name}}</h4>
          <div class="info">
            <span>
              ¥ {{item.price}}元
            </span>
          </div>
        </li>
      </ul>
      <!--分页区-->
      <el-row>
        <el-col :span="24" style="text-align: center">
          <div class="page">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pagenum"
              :page-sizes="[20,30,40,50,60]"
              :page-size="queryInfo.pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </el-col>
      </el-row>
      <HomeFooter></HomeFooter>
    </div>
    <!--抱歉没有搜索到图书信息-->
    <div v-if="bookCategoryByIdList.length===0">
      <div class="tips-words">
        <i></i>
        <div>
          <h3>非常抱歉！没有找到相关的商品。<a href="/">点击这里返回首页</a></h3>
          <dl>
            <dt>建议您:</dt>
            <dd>1.检查输入的文字是否有误</dd>
            <dd>2.告诉我们您想找的商品，我们马上进货！</dd>
            <dd>
              <span>您还可以去看看：</span>
              <em>
                <a href="#">iphone 7</a>
                <a href="#">三星手机</a>
                <a href="#">洗衣机</a>
                <a href="#">热水器</a>
                <a href="#">挂壁式空调</a>
                <a href="#">空调</a>
                <a href="#">美的空调</a>
                <a href="#">床</a>
              </em>
            </dd>

          </dl>
        </div>
      </div>
      <HomeFooter></HomeFooter>
    </div>
  </div>
</template>

<script>
import HomeFooter from "./HomeFooter";
export default {
  name: "CategoryBookList",
  data(){
    return{
      //获取所有图书列表参数对象
      queryInfo: {
        categoryId:'',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 20,
      },
      //根据类别id查询的图书
      bookCategoryByIdList: [],
      //总条数
      total: 0,
      //类别名称
      category_name:''
    }
  },
  created() {
    //根据图书类别ID查询图书
    this.bookcategoryById()
  },
  methods:{
    //更具图书类比ID查询图书
    bookcategoryById(){
      if (this.$route.query.category_id==''||this.$route.query.category_id==undefined){
        this.$router.push('/')
      }
      this.queryInfo.categoryId=this.$route.query.category_id
      this.category_name=this.$route.query.category_name
      this.$http.get('/public/bookcategoryById',{params:this.queryInfo}).then(res=>{
        if (res.data.code!==200){
          return this.$message.error('查询所属类别失败')
        }
        this.bookCategoryByIdList=res.data.data.list
        this.total=res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.bookcategoryById()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.bookcategoryById()
    },
    //将图书id通过路由地址携带到另一个组件中查询图书的详情
    goBookInfo(book_id){
      this.$router.push('/bookinfo?book_id='+book_id)
    }
  },
  components:{
    HomeFooter
  }
}
</script>

<style scoped>
.tips-words{
  width: 1200px;
  height: 300px;
  margin: 0 auto;
  margin-top: 10px;
  background-color: #FFF5ED;
}

.tips-words i{
  width: 54px;
  height: 68px;
  margin: 116px 0 116px 60px;
  background-position: -239px -152px;
  display: block;
  float: left;
  padding:0 20px 0 0;
  background: url("https://res.suning.cn/project/search/searchpage_new/css/images/pic.png?20210126") -165px -153px no-repeat;;
}

.tips-words div{
  float: left;
  margin-top: 102px;
}

.tips-words div h3{
  color: #666666;
}

.tips-words div dl{
  color: #666666;
  font-size: 12px;
}

.tips-words div dl dd em a{
  color: #666666;
  font-style: normal;
}






.booklist {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.booklist a{
  display: block;
  height: 20px;
  text-align: right;
  padding-right: 36px;
  color: #646464;
}

.booklist ul {
  width: 1200px;
  margin-top: 10px;
}

.booklist ul li {
  float: left;
  width: 225px;
  height: 300px;
  background-color: #FFFFFF;
  margin-right: 8px;
  margin-bottom: 15px;
  border: 1px solid #f3f3f3;
}

.booklist ul li:hover {
  z-index: 2; /*设置在顶层显示*/
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1); /*添加阴影*/
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0); /*向上浮动*/
  transform: translate3d(0, -2px, 0);
}

.booklist ul li > img {
  width: 150px;
  height: 150px;
  margin: 20px 0 10px 35px;
}

.booklist ul li h4 {
  height: 50px;
  padding: 10px 20px 0;
  font-size: 12px;
  font-weight: 400;
  overflow: hidden;
}

.booklist ul li h4:hover {
  color: #ff7c2d;
}

.booklist ul li .info {
  margin: 15px 20px 0 25px;
}

.booklist ul li .info span {
  font-size: 16px;
  color: #DD0000;
}

/*清除浮动*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}
</style>
