<!--登录-->
<template>
  <div class="login_container">
    <div class="login_box">
      <!--头像区域-->
      <div class="avatar_box">
        <img src="../../assets/images/logo.png">
      </div>
      <!--登录表单区域-->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">

        <!--用户名-->
        <el-form-item prop="admin_name">
          <el-input v-model="loginForm.admin_name" prefix-icon="el-icon-user-solid"></el-input>
        </el-form-item>

        <!--密码-->
        <el-form-item prop="admin_password">
          <el-input v-model="loginForm.admin_password" prefix-icon="el-icon-s-goods" type="password" @keyup.enter.native="adminLogin"></el-input>
        </el-form-item>

        <!--按钮区域-->
        <el-form-item class="btns">
          <el-button type="primary" @click="adminLogin">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
          <el-link type="primary" style="margin-left: 10px" @click="goHome">返回首页</el-link>
        </el-form-item>

      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      //这是登录表单的数据绑定对象
      loginForm: {
        admin_name: '',
        admin_password: '',
      },
      //这是表单的验证规则对象
      loginFormRules: {
        //验证用户名是否合法
        admin_name: [
          {
            required: true,//必填
            message: '请输入用户名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ],
        //验证密码是否合法
        admin_password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: '长度在 6 到 15 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    //点击重置按钮，重置登录表单
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },

    //登录
    adminLogin () {
      //点击登录的时候先调用validate方法验证表单内容是否有误
      this.$refs.loginFormRef.validate(async valid => {//valid表示validata表单预验证的返回结果，是个布尔值
        //如果valid参数为true则验证通过
        if (!valid) return

        //发送请求
        this.$http.post('/admin/login',this.loginForm,{emulateJSON:true}).then(res=>{
            console.log(res)
          if (res.data.code!==200||res.data.data.loginNo==='loginNo'){
            return this.$message.error('登录失败')
          }
          this.$message.success('登录成功')
          this.$router.push('/adminHome')
        })
      })
    },
    //返回首页
    goHome(){
      this.$router.push('/home')
    }
  }
}
</script>

<style scoped lang="less">
.login_container {
  background-color: #2b4b6b;
  height: 100%;
  background-image:url("../../assets/images/pexels-photo-3704611.jpg");
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0px 0 50px;
  overflow-y: hidden;
  position: relative;
  font-family: 'Montserrat', sans-serif;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}


</style>
