<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>已发货订单</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-table :data="shippedOrderList" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="商品图片" prop="book_img">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.book_img"></el-image>
          </template>
        </el-table-column>
        <el-table-column label="商品名称" prop="book_name"></el-table-column>
        <el-table-column label="收货人" prop="nick_name"></el-table-column>
        <el-table-column label="收货人电话" prop="tel"></el-table-column>
        <el-table-column label="收货地址" prop="address"></el-table-column>
        <el-table-column label="购买的数量" prop="buy_num"></el-table-column>
        <el-table-column label="商品单价" prop="buy_price"></el-table-column>
        <el-table-column label="订单总价">
          <template slot-scope="scope">
            <span>
              {{ (scope.row.buy_num * scope.row.buy_price).toFixed(2) }}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="订单状态" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.status===1">
              已发货
            </span>
            <span v-if="scope.row.status===2">
              用户已收货
            </span>
          </template>
        </el-table-column>
        <el-table-column label="订单操作" width="100">
          <template slot-scope="scope">
            <el-button type="danger" @click="removeOrderById(scope.row.order_id)" v-if="scope.row.status===2">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>


  </div>
</template>

<script>
export default {
  data() {
    return {
      //查询的条件
      queryInfo: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5
      },
      //总条数
      total: 0,
      //所有已发货订单列表
      shippedOrderList: []
    }
  },
  created() {
    //获取已发货订单列表
    this.getShippedOrderList()
  },
  methods: {
    //获取已发货订单列表
    getShippedOrderList() {
      this.$http.get('/admin/getShippedOrderList', {params: this.queryInfo}).then(res => {
        console.log(res)
        if (res.data.code !== 200) {
          return this.$message.error('获取订单列表失败')
        }
        this.shippedOrderList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //删除订单
    async removeOrderById(order_id) {
      console.log(order_id)
      const confirmResult = await this.$confirm('确定删除此订单吗, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/admin/removeOrderById?order_id=' + order_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除订单失败')
        }
        this.$message.success('删除订单成功')
        this.getShippedOrderList()
      })

    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}
</style>
