<template>
  <div class="userFeedback">
    <!--头部，返回首页和返回上一级页面-->
    <div class="home clearfix">
      <a href="/">返回首页</a>
      <a @click="previousPage">返回上一页</a>
    </div>
    <el-card>
      <h3>{{ user.username }}的反馈信息</h3>
      <el-row :gutter="10" style="margin-top: 10px;">
        <el-col :span="8">
          <el-input type="textarea" placeholder="请输入需要反馈的内容"
                    v-model="textarea" maxlength="30"
                    show-word-limit></el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" round @click="addFeedback" style="margin-top: 6px">反馈</el-button>
        </el-col>
      </el-row>
      <el-table :data="feedbackList" style="width: 100%">
        <el-table-column type="index" width="100"></el-table-column>
        <el-table-column prop="feedback_info" label="反馈内容" width="300"></el-table-column>
        <el-table-column prop="feedback_time" label="反馈时间" width="300">
          <template slot-scope="scope">
            {{ scope.row.feedback_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="feedback_read" label="管理员是否已读" width="400">
          <template slot-scope="scope">
            <span v-if="scope.row.feedback_read===0">管理员未读</span>
            <span v-else>管理员已读</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-tooltip content="删除此反馈信息" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle
                         @click="removeFeedbackById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    return {
      //用户
      user: {},
      //查询信息
      queryInfo: {
        //用户id
        user_id: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 4,
      },
      //用户收藏列表
      feedbackList: [],
      //总条数
      total: 0,
      //用户反馈输入的内容
      textarea: '',
      //随机查询出来的8条数据
      randomBookList: [],
    }
  },
  created() {
    //从sessionStorage取出已经登录的用户
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //获取用户的收藏列表
    this.getFeedbackList()
  },
  methods: {
    //获取用户的收藏列表
    getFeedbackList() {
      this.queryInfo.user_id = this.user.user_id
      this.$http.get('/user/getFeedbackList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取用户反馈失败')
        }
        this.feedbackList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getFeedbackList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getFeedbackList()
    },
    //用户添加反馈信息
    addFeedback() {
      if (this.textarea === '' || this.textarea.trim() === '') {
        return this.$message.info('请输入反馈信息')
      }
      this.$http.post('/user/addFeedback', {
        user_name: this.user.username,
        feedback_info: this.textarea.trim(),
        user_id: this.user.user_id
      }, {emulateJSON: true}).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('添加反馈信息失败')
        }
        this.$message.success('添加反馈信息成功')
        this.textarea = ''
        this.getFeedbackList();
      })
    },
    //商品详情
    bookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //删除评论
    async removeFeedbackById(id) {
      const confirmResult = await this.$confirm('是否删除此反馈信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/user/removeFeedbackById?id=' + id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除失败')
        }
        this.$message.success('删除反馈信息成功')
        this.getFeedbackList()
      })
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}
.userFeedback{
  width: 1260px;
  margin: 0 auto;
  margin-top: 10px;
}
.el-card {
  width: 100%;
  margin: 10px auto 0;
}

.name-a {
  cursor: pointer
}

.el-pagination {
  text-align: center;
}

.main-item {
  width: 1200px;
  margin: 0 auto;
  /*background-color: deepskyblue;*/
  height: 200px;
}

.main-title {
  text-align: center
}

.book-item {
  width: 240px;
  height: 350px;
  background-color: white;
  margin: 0 20px 20px 20px;
  border: 1px none;
  text-align: center;
}

.book-item img {
  display: block;
  margin: 20px auto;
  width: 220px;
  height: 220px;
}

.book-item:hover {
  box-shadow: 10px 10px 10px 5px #e1e1e1;
}

.book-item > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}
</style>
