<!--用户管理-->
<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图-->
    <el-card>
      <!--搜索与添加区域  el-row行，el-col列-->
      <el-row>
        <el-col :span="5">
          <el-input placeholder="输入用户名关键字" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--用户列表区  border是竖线，:stripe是隔行变色-->
      <el-table :data="userlist" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="用户头像">
          <template slot-scope="scope">
            <el-avatar :size="100" :src="scope.row.user_img" v-if="scope.row.user_img!==null"></el-avatar>
            <span v-else>用户还未上传头像</span>
          </template>
        </el-table-column>
        <el-table-column label="用户名">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ scope.row.username }}</p>
              <p>性别: {{ scope.row.gender }}</p>
              <p>年龄: {{ scope.row.age }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.username }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="password" label="密码"></el-table-column>
        <el-table-column prop="real_name" label="真实姓名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="address" label="地址"></el-table-column>
        <el-table-column prop="postcode" label="邮编"></el-table-column>
        <el-table-column prop="register_time" label="注册时间">
          <template slot-scope="scope">
            {{ scope.row.register_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150px">
          <template slot-scope="scope">
            <!--删除按钮-->
            <el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeUserById(scope.row.user_id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--分页区域
          @size-change: :page-sizes改变时会触发绑定的方法，这个方法会传一个参数，这个参数就是改变后的值
          @current-change：当前页码值改变时会触发绑定的方法，这个方法会带一个参数，这个参数就是改变后的值
          :current-page：当前是第几页
          :page-sizes：供用户选择每页显示几条数据，这个改变时，会触发@size-change所绑定的函数
          :page-size：当前每页显示多少条数据
          :total：显示总共多少条数据
          background:给分页的页码条加一个背景
          -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //获取用户列表参数对象
      queryInfo: {
        query: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 3
      },
      //所有的用户
      userlist: [],
      //总页数
      total: 0,
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    //获取用户列表
    getUserList() {
      this.queryInfo.query=this.queryInfo.query.trim()
      this.$http.get('/admin/getUserList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取用户列表失败')
        }
        this.userlist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },

    //根据用户的id删除用户信息
    async removeUserById(id) {
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/admin/removeUserById?userid=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除用户失败')
        }
        this.$message.success('删除此用户成功')
        this.getUserList()
      })
    },
  }
}
</script>

<style scoped lang="less">
.el-row {
  margin-left: 4px !important;
}

.el-col {
  margin-left: -4px;
}

.el-table {
  margin-top: 10px;
}

.el-card {
  margin-top: 10px;
}

.el-pagination{
  width: 50%;
  margin: 0 auto;
}
</style>
