<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-row>
        <el-col :span="5">
          <el-button type="primary" @click="oneKeyOrder">一键发货</el-button>
        </el-col>
      </el-row>

      <el-table :data="order" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="商品图片" prop="book_img">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.book_img"></el-image>
          </template>
        </el-table-column>
        <el-table-column label="商品名称" prop="book_name"></el-table-column>
        <el-table-column label="收货人" prop="nick_name"></el-table-column>
        <el-table-column label="收货人电话" prop="tel"></el-table-column>
        <el-table-column label="收货地址" prop="address"></el-table-column>
        <el-table-column label="购买的数量" prop="buy_num"></el-table-column>
        <el-table-column label="商品单价" prop="buy_price"></el-table-column>
        <el-table-column label="订单总价">
          <template slot-scope="scope">
            <span>
              {{ (scope.row.buy_num * scope.row.buy_price).toFixed(2) }}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="订单状态" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.status===0">
              已付款，静等发货
            </span>
            <span v-if="scope.row.status===1">
              已发货
            </span>
          </template>
        </el-table-column>
        <el-table-column label="订单操作" width="100">
          <template slot-scope="scope">
            <el-button type="success" @click="updataOrder(scope.row.order_id)">发货</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //查询的条件
      queryInfo: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5
      },
      //订单列表
      order: [],
      //总条数
      total: 0
    }
  },
  created() {
    //获取所有订单
    this.getOrderList()
  },
  methods: {
    //获取所有订单
    getOrderList() {
      this.$http.get('/admin/getOrderList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取订单列表失败')
        }
        this.order = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    //发货
    updataOrder(order_id) {
      this.$http.post('/admin/updataOrder?order_id=' + order_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('发货失败')
        }
        this.$message.success('发货成功')
        this.getOrderList()
      })
    },
    //一键发货
    async oneKeyOrder(){
      const confirmResult = await this.$confirm('确定一键发货？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.post('/admin/oneKeyOrder').then(res=>{
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('一键发货失败')
        }
        this.$message.success('一键发货成功')
        this.getOrderList()
      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
