import Vue from 'vue'
import App from './App.vue'
import router from './router'
//导入进度条
import Nprogress from 'nprogress'
//导入进度条的样式
import 'nprogress/nprogress.css'
//导入element-ui
import './plugins/element.js'
//导入全局样式表
import "./assets/css/global.css"
//导入vue-parti'cles，登录页面的粒子特效
import VueParticles from "vue-particles";
//全局导入asios包
import axios from "axios";

Vue.config.productionTip=false

Vue.use(VueParticles)

//设置cross跨域 并设置访问权限 允许跨域携带cookie信息，
// 如果不携带cookie信息，那么跨域每次请求服务器都会重新创建一个session，因为session的JSESSIONID是保存在浏览器的cookie中的
axios.defaults.withCredentials = true;
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

//配置请求时候的根路经
axios.defaults.baseURL = 'http://localhost:8080'

/*
* request就是一个请求的拦截器，每次请求服务器之前会先执行use回调函数
* 除了登录之外，每次请求都需要在请求头中添加一个Authorization属性
* 这样做是为了只有登录成功以后才能执行后续的操作
* */
axios.interceptors.request.use(config=>{
  //在response拦截器中，展示进度条
  Nprogress.start()

  return config
})

//在response拦截器中隐藏进度条
axios.interceptors.response.use(config=>{
  Nprogress.done()
  return config
})

axios.interceptors.request.use(
  config => {
    let url = config.url
    // get参数编码
    if (config.method === 'get' && config.params) {
      url += '?'
      let keys = Object.keys(config.params)
      for (let key of keys) {
        url += `${key}=${encodeURIComponent(config.params[key])}&`
      }
      url = url.substring(0, url.length - 1)
      config.params = {}
    }
    config.url = url
    return config
  },
)

//把axios这个包挂载到vue原型对象上
Vue.prototype.$http = axios

//创建过滤器将秒数过滤为年月日
Vue.filter('dateFormat', function (originVal) {
  const dt = new Date(originVal)
  const y = dt.getFullYear()
  //月份是0-11所以加1，不足两位就在前面补零
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  //日是0-11所以加1，不足就在前面补零
  const d = (dt.getDate() + '').padStart(2, '0')

  return `${y}-${m}-${d}`
})

//创建过滤器将秒数过滤为年月日，时分秒
Vue.filter('dateFormat1', function (originVal) {
  const dt = new Date(originVal)
  const y = dt.getFullYear()
  //月份是0-11所以加1，不足两位就在前面补零
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  //日是0-11所以加1，不足就在前面补零
  const d = (dt.getDate() + '').padStart(2, '0')
  //时，不足就在前面补零
  const hh = (dt.getHours() + '').padStart(2, '0')
  //分，不足就在前面补零
  const mm = (dt.getMinutes() + '').padStart(2, '0')
  //秒，不足就在前面补零
  const ss = (dt.getSeconds() + '').padStart(2, '0')

  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})

Vue.config.productionTip = false

new Vue({

  router,
  render: h => h(App)
}).$mount('#app')
