<template>
  <div class="userinfo">
    <!--头部，返回首页和返回上一级页面-->
    <div class="home clearfix">
      <a href="/">返回首页</a>
      <a @click="previousPage">返回上一页</a>
    </div>

    <!--<el-descriptions title="个人中心">
      <el-descriptions-item label="头像">
        <el-avatar :size="100" :src="user.user_img"></el-avatar>
      </el-descriptions-item>
      <el-descriptions-item label="用户名">{{ user.username }}</el-descriptions-item>
      <el-descriptions-item label="用户密码">{{ user.password }}</el-descriptions-item>
      <el-descriptions-item label="真实姓名">{{ user.real_name }}</el-descriptions-item>
      <el-descriptions-item label="性别">{{ user.gender }}</el-descriptions-item>
      <el-descriptions-item label="年龄">{{ user.age }}</el-descriptions-item>
      <el-descriptions-item label="电话号码">{{ user.phone }}</el-descriptions-item>
      <el-descriptions-item label="邮箱">
        <el-tag size="small">{{ user.email }}</el-tag>
      </el-descriptions-item>
      <el-descriptions-item label="地址">{{ user.address }}</el-descriptions-item>
      <el-descriptions-item label="邮编">{{ user.postcode }}</el-descriptions-item>
      <el-descriptions-item label="注册时间">{{ user.register_time|dateFormat1 }}</el-descriptions-item>
      <el-descriptions-item label="修改头像">
        <el-upload class="upload-demo" drag accept="image/jpg" with-credentials
                   :action="uploadURL" multiple name="userImg" :on-success="fileUpload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-descriptions-item>
    </el-descriptions>-->
    <el-descriptions class="margin-top" title="个人中心" :column="3" border>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-user"></i>
          用户名
        </template>
        {{ user.username }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-unlock"></i>
          用户密码
        </template>
        {{ user.password }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-mobile-phone"></i>
          手机号
        </template>
        {{ user.phone }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-location-outline"></i>
          地址
        </template>
        {{ user.address }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          真实姓名
        </template>
        <el-tag size="small">{{ user.real_name }}</el-tag>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-s-custom"></i>
          性别
        </template>
        {{ user.gender }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-star-off"></i>
          年龄
        </template>
        {{ user.age }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-s-cooperation"></i>
          邮箱
        </template>
        {{user.email}}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          邮编
        </template>
        {{ user.postcode }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-date"></i>
          注册时间
        </template>
        {{ user.register_time|dateFormat1 }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-picture-outline"></i>
          头像
        </template>
        <el-avatar :size="100" :src="user.user_img"></el-avatar>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-picture-outline-round"></i>
          修改头像
        </template>
        <el-upload class="upload-demo" drag accept="image/jpg" with-credentials
                   :action="uploadURL" multiple name="userImg" :on-success="fileUpload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-descriptions-item>

    </el-descriptions>

    <div class="tooltip">
      <el-tooltip content="修改个人信息" placement="top" :enterable="false">
        <el-button type="primary" icon="el-icon-edit" circle @click="editDialogVisibleBox"></el-button>
      </el-tooltip>
      <el-tooltip content="注销账号" placement="top" :enterable="false">
        <el-button type="danger" icon="el-icon-delete" circle @click="cancelAccount"></el-button>
      </el-tooltip>
      <el-tooltip content="我的收藏" placement="top" :enterable="false">
        <el-button type="success" icon="el-icon-star-off" circle @click="goCollection"></el-button>
      </el-tooltip>
      <el-tooltip content="反馈" placement="top" :enterable="false">
        <el-button type="info" icon="el-icon-message" circle @click="goFeedback"></el-button>
      </el-tooltip>
    </div>



    <!--修改用户对话框-->
    <el-dialog title="修改用户信息" :visible.sync="editDialogVisible" width="30%" @close="editDialogClosed">
      <el-form :model="user1" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="用户名">
          <el-input v-model="user1.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="user1.password"></el-input>
        </el-form-item>
        <el-form-item label="真实姓名" prop="real_name">
          <el-input v-model="user1.real_name"></el-input>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="user1.age" type="number" min="1" max="100"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="user1.gender">
            <el-radio label="男">男</el-radio>
            <el-radio label="女">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="电话号码" prop="phone">
          <el-input v-model="user1.phone"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="user1.email"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="user1.address"></el-input>
        </el-form-item>
        <el-form-item label="邮编" prop="postcode">
          <el-input v-model="user1.postcode"></el-input>
        </el-form-item>
        <el-form-item label="注册日期">
          {{ user.register_time|dateFormat1 }}
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editUserInfo">确 定</el-button>
  </span>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";
export default {
  data() {
    //验证邮箱的规则
    var checkEmail = (rule, value, cd) => {
      //验证邮箱的正则表达式
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        //合法邮箱
        return cd()
      }
      cd(new Error('请输入合法的邮箱'))
    }

    //验证手机号的规则
    var checkMobile = (rule, value, cd) => {
      //验证手机号的正则表达式
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //合法电话
        return cd()
      }
      cd(new Error('请输入正确的电话号'))
    }
    return {
      //用户
      user: {},
      //对话框的用户信息
      user1: {},
      //上传图片的URL地址
      uploadURL: 'http://localhost:8080/user/uploadUserTx',
      //控制修改用户信息对话框显示与隐藏
      editDialogVisible: false,
      //修改表单的验证规则对象
      editFormRules: {
        password: [
          {
            required: true,//必填
            message: '请输入密码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 6,
            max: 15,
            message: '用户名的长度在6~15个字符之间',
            trigger: 'blur'
          }
        ],
        real_name: [
          {
            required: true,//必填
            message: '请输入真实姓名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 8,
            message: '真实姓名的长度在2~8个字符之间',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,//必填
            message: '请输入电话号码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkMobile,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        email: [
          {
            required: true,//必填
            message: '请输入邮箱',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkEmail,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        address: [
          {
            required: true,//必填
            message: '请输入地址',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 6,
            max: 15,
            message: '地址的长度在6~15个字符之间',
            trigger: 'blur'
          }
        ],
        postcode: [
          {
            required: true,//必填
            message: '请输入邮编',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 5,
            max: 8,
            message: '邮编的长度在5~8个字符之间',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    //从sessionStorage取出已经登录的用户
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //根据用户id查询用户详情
    this.userInfo()
  },
  methods: {
    //文件上传成功会触发这个函数，参数：userImg是文件上传组件中action属性请求服务器后返回来的值
    fileUpload(userImg) {
      this.$http.get('/user/updataUserTx', {
        params: {
          user_id: this.user.user_id,
          user_img: userImg
        }
      }).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('修改头像失败')
        }
        this.$message.success('修改头像成功')
        this.userInfo()
      })
    },
    //根据用户id查询用户详情
    userInfo() {
      this.$http.get('/user/userInfo?user_id=' + this.user.user_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('查询用户详情失败')
        }

        this.user = res.data.data
        //将sessionStorage中的数据更新为数据库中的数据
        window.sessionStorage.setItem('user', JSON.stringify(this.user))
      })
    },
    //监听修改用户对话框的关闭事件，重置表单验证
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //修改用户信息
    editUserInfo() {
      //用户点击修改表单中的确定按钮之后，验证表单数据
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return this.$message.error('请填写完整用户信息')
        /*//使用URLSearchParams来将值保存，用于传入后台
        let param = new URLSearchParams();
        //将用户信息封装成一个JSON格式的字符串，传递给后端
        param.append('user', JSON.stringify(this.user1))*/

        var user=JSON.stringify(this.user1)
        this.$http.get('/user/editUserInfo', {params: {user:user}}).then(res => {
          if (res.data.code !== 200 || res.data.data <= 0) {
            return this.$message.error('修改信息失败')
          }
          this.$message.success('修改信息成功')
          this.userInfo()
          this.editDialogVisible = false
        })
      })
    },
    //显示修改对话框，同时查询用户的信息，将信息展示到对话框中
    editDialogVisibleBox() {
      this.$http.get('/user/userInfo?user_id=' + this.user.user_id).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('查询用户详情失败')
        }
        this.user1 = res.data.data
        this.editDialogVisible = true
      })
    },
    //根据用户id删除用户(注销账号)
    async cancelAccount() {
      const confirmResult = await this.$confirm('确定注销此账号?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
    },
    //去用户收藏
    goCollection(){
      this.$router.push('/usercollection')
    },
    //去用户反馈
    goFeedback(){
      this.$router.push('/userfeedback')
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.tooltip{
  margin-top: 10px;
}

.home {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
}

.home a {
  float: right;
  display: inline-block;
  height: 20px;
  text-align: right;
  padding-right: 30px;
  color: #646464;
}

.userinfo{
  width: 1200px;
  margin: 10px auto 0;
}
</style>
