<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加图书</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <!-- 消息提示 -->
      <el-alert title="添加图书信息" type="info" center show-icon :closable="false"></el-alert>

      <!--步骤条-->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="图书基本信息"></el-step>
        <el-step title="图书类别"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品介绍"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!--tab栏区
        注意：form表单不能嵌套在tabs标签页中 标签页只允许tab-pane当它的子节点，所以form表单需要包裹整个tabs标签页
        label-position：指定表单域标签的位置
        :before-leave:切换标签之前的钩子，若返回 false 或者返回 Promise 且被 reject，则阻止切换。
        @tab-click="tabClicked":点击tab栏触发时间，获取动态参数或静态属性的列表数据-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-position="left" :inline="true">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave">
          <el-tab-pane label="图书基本信息" name="0">
            <el-form-item label="书名" prop="book_name">
              <el-input v-model="addForm.book_name"></el-input>
            </el-form-item>
            <el-form-item label="作者" prop="author">
              <el-input v-model="addForm.author"></el-input>
            </el-form-item>
            <el-form-item label="出版社" prop="book_man">
              <el-input v-model="addForm.book_man"></el-input>
            </el-form-item>
            <el-form-item label="单价" prop="price">
              <el-input v-model="addForm.price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="库存" prop="total">
              <el-input v-model="addForm.total" type="number"></el-input>
            </el-form-item>
            <el-form-item label="进库的时间" prop="add_time">
              <el-date-picker v-model="addForm.add_time" type="datetime" placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="图书类别" name="1">
            <el-select v-model="addForm.category_id" placeholder="请选择类别" filterable>
              <el-option v-for="item in categoryList" :key="item.id" :label="item.category_name"
                         :value="item.category_id">
              </el-option>
            </el-select>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="2">
            <el-upload :action="uploadURL"
                       :on-preview="handlePreview"
                       :on-remove="handleRemove"
                       list-type="picture" :on-success="handleSuccess" :limit='1' v-model="addForm.book_img"
                       name="bookImg">
              <el-button size="small" type="primary">点击上传</el-button>
              <!--<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="图书介绍" name="3">
            <el-form-item label="图书介绍" prop="introduction">
              <el-input type="textarea" :rows="2" placeholder="请输入图书介绍"
                        v-model="addForm.introduction" :autosize="{ minRows: 2, maxRows: 4}">
              </el-input>
              <el-button type="primary" class="btnAdd" @click="add" style="margin-top: 46px">添加商品</el-button>
            </el-form-item>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 预览图片对话框 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="250px">
      <img :src="previewPath" class="previewImg"/>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //控制tabs标签页和步骤条的联动属性
      activeIndex: '0',
      //添加图书的表单数据对象
      addForm: {
        //图书名
        book_name: '',
        //作者
        author: '',
        //出版社
        book_man: '',
        //单价
        price: '',
        //所属类别
        category_id: '',
        //库存
        total: '',
        //图书图片
        book_img: '',
        //进库的时间
        add_time: '',
        //图书介绍
        introduction: '',
      },
      //添加表单的验证规则对象
      addFormRules: {
        book_name: [
          {
            required: true,//必填
            message: '请输入图书名称',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 15,
            message: '用户名的长度在2~15个字符之间',
            trigger: 'blur'
          }
        ],
        author: [
          {
            required: true,//必填
            message: '请输入作者姓名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 6,
            message: '用户名的长度在2~6个字符之间',
            trigger: 'blur'
          }
        ],
        book_man: [
          {
            required: true,//必填
            message: '请输入出版社',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            message: '出版社最少不能低于两个字符',
            trigger: 'blur'
          }
        ],
        price: [
          {
            required: true,//必填
            message: '请输入单价',
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        total: [
          {
            required: true,//必填
            message: '请输入库存',
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        introduction: [
          {
            required: true,//必填
            message: '请输入图书介绍',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            message: '图书介绍最少不能低于两个字符',
            trigger: 'blur'
          }
        ]
      },
      //所有分类
      categoryList: [],
      //上传图片的地址
      uploadURL: 'http://localhost:8080/admin/uploadBookImg',
      //控制预览图片对话框的显示和隐藏
      previewVisible: false,
      //保存预览图片的url地址
      previewPath: '',
    }
  },
  created() {
    //获取所有类别
    this.getCategoryList()
  },
  methods: {
    //切换标签页的时候会触发这个函数
    //参数activeName：即将进入的标签页的name名称  参数oldActiveName：即将离开的标签页的名称
    beforeTabLeave(activeName, oldActiveName) {
      //在第一个标签页的时候
      if (oldActiveName === '0' && this.addForm.book_name == '' || this.addForm.author == '' ||
        this.addForm.book_man == '' || this.addForm.price == '' || this.addForm.total == '' || this.addForm.add_time == '') {
        this.$message.info('请填写图书的基础信息')
        return false
      }

      if (oldActiveName === '1' && this.addForm.category_id == '') {
        this.$message.info('请选择图书类别')
        return false
      }

      if (oldActiveName === '2' && this.addForm.book_img == '') {
        this.$message.info('请上传图书图片')
        return false
      }
    },
    //获取所有图书类别
    getCategoryList() {
      this.$http.get('/admin/addBookGetCategoryList').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取所有类别失败')
        }
        this.categoryList = res.data.data
      })
    },
    //处理图片预览效果
    //形参file就是用户预览的那个文件
    handlePreview(file) {
      this.previewVisible = true
    },
    //处理移除图片的操作
    //形参file就是用户点击删除的文件
    handleRemove(file) {
      this.addForm.book_img = ''
    },
    //监听图片上传成功的事件
    //参数：response就是服务器返回的数据对象
    handleSuccess(response) {
      this.previewPath = response.upload
      this.addForm.book_img = response.upload
    },
    //添加商品
    add() {
      if (this.addForm.introduction === '') {
        return this.$message.info('请填写图书介绍')
      }
      this.$refs.addFormRef.validate(valid => {
        if (!valid) {
          return this.$message.error('请填写必要的表单项')
        }
        this.$http.post('/admin/addBook', this.addForm, {emulateJSON: true}).then(res => {
            console.log(res)
          if (res.data.code!==200||res.data.data<=0){
            return this.$message.error('添加图书失败')
          }
          this.$message.success('添加图书成功')
          this.$router.push('/adminbook')
        })
      })
    },
    //

  }
}
</script>

<style scoped>
.el-steps {
  width: 50%;
  margin: 0 auto;
  margin-top: 10px;
}

.el-card {
  margin-top: 10px;
}
</style>
