<template>
  <div class="order">
    <!--头部，返回首页和返回上一级页面-->
    <div class="home clearfix">
      <a href="/">返回首页</a>
      <a @click="previousPage">返回上一页</a>
    </div>
    <!--导航部分-->
    <!--当有订单信息的时候-->
    <el-row v-if="order.length>0">
      <el-col>
        <el-table :data="order" style="width: 100%">
          <!--展开列-->
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-row>
                <el-col :span="8">
                  <span>收货人：{{ scope.row.nick_name }}</span>
                </el-col>
                <el-col :span="8">
                  <span>收货人电话：{{ scope.row.tel }}</span>
                </el-col>
                <el-col :span="8">
                  <span>收货地址：{{ scope.row.address }}</span>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <el-table-column prop="date" label="商品名称" width="300">
            <template slot-scope="scope">
              <a class="name-a" @click="getBookInfo(scope.row.book_id)">
                <img :src="scope.row.book_img" style="height: 92px;width: 60px"/>
                <span>{{ scope.row.book_name }}</span>
              </a>
            </template>
          </el-table-column>
          <el-table-column prop="buy_price" label="单价" width="130" sortable></el-table-column>
          <el-table-column prop="buy_num" label="购买数量" width="130"></el-table-column>
          <el-table-column prop="buy_time" label="订购时间" width="170" sortable>
            <template slot-scope="scope">
                <span>
                  {{ scope.row.buy_time|dateFormat1 }}
                </span>
            </template>
          </el-table-column>

          <el-table-column label="订单总价" width="180" class="el-icon-user">
            <template slot-scope="scope">
                            <span>
                                {{ (scope.row.buy_num * scope.row.buy_price).toFixed(2) }}
                            </span>
            </template>
          </el-table-column>
          <el-table-column label="订单状态" width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.status===0">
                已付款，静等发货
              </span>
              <span v-if="scope.row.status===1">
                已发货
              </span>
              <span v-if="scope.row.status===2">
                快去看看吧
              </span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">
              <div v-if="scope.row.status===1">
                <el-button type="primary" size="mini" @click="receiving(scope.row.order_id)">确定收货</el-button>
              </div>

              <div v-if="scope.row.status===2">
                <el-button type="danger" @click="deleteOrderById(scope.row.order_id)" size="mini">删除</el-button>
                <el-button type="primary" @click="commentDialogBox(scope.row.book_id)" size="mini">评论商品</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <!--当没有订单信息的时候-->
    <div v-if="order.length===0">
      <div class="tips-words">
        <i></i>
        <div>
          <h3>您的订单空空如也！<a href="/">去逛逛</a></h3>
          <dl>
            <dt>建议您:</dt>
            <dd>1.看看您感兴趣的图书</dd>
            <dd>2.告诉我们您想找的商品，我们马上进货！</dd>
          </dl>
        </div>
      </div>
      <HomeFooter></HomeFooter>
    </div>

    <!--分页-->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!--评论对话框-->
    <el-dialog title="发表评论" :visible.sync="commentVisible" width="30%">
      <el-input type="textarea" placeholder="请输入内容" v-model="textarea" maxlength="30" show-word-limit></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="commentVisible = false" style="">取 消</el-button>
        <el-button type="primary" @click="addComment">发 表</el-button>
      </span>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>
</template>

<script>
import HomeFooter from "../home/HomeFooter";

export default {
  data() {
    return {
      //用户
      user: {},
      //传递的服务器的数据
      queryInfo: {
        user_id: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5,
      },
      //用户的订单列表
      order: [],
      //总条数
      total: 0,
      //控制评论对话框显示与隐藏
      commentVisible: false,
      //用户评论的内容
      textarea: '',
      //图书id
      book_id: '',
      //添加评论的数据对象，需要传给后端
      addCommentForm: {
        user_id: '',
        user_name: '',
        comment_info: '',
        book_id: '',
        book_name: '',
        user_img: ''
      },
      //图书详情
      book: {},
    }
  },
  created() {
    this.user = JSON.parse(window.sessionStorage.getItem('user'));
    //获取用户的订单
    this.getUserOrderList(this.user.user_id)
  },
  methods: {
    //获取用户的订单
    getUserOrderList(user_id) {
      this.queryInfo.user_id = user_id
      this.$http.get('/user/getUserOrderList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取用户订单列表失败')
        }
        this.total = res.data.data.total
        this.order = res.data.data.list
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserOrderList(this.user.user_id)
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserOrderList(this.user.user_id)
    },
    //查看图书详情
    getBookInfo(book_id) {
      this.$router.push('/bookinfo?book_id=' + book_id)
    },
    //去首页
    home() {
      this.$router.push('/home')
    },
    //根据id删除订单
    async deleteOrderById(order_id) {
      const confirmResult = await this.$confirm('是否删除该订单信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.get('/user/deleteOrderById?id=' + order_id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除订单失败')
        }
        this.$message.success('删除订单成功')
        this.getUserOrderList(this.user.user_id)
      })
    },
    //显示评论对话框，并保存图书id
    commentDialogBox(book_id) {
      //保存图书id
      this.book_id = book_id
      //评论图书之前先查询图书详情将图书名称取出来
      this.$http.get('/public/getBookDetails', {params: {book_id: this.book_id}}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取图书详情失败')
        }
        this.book = res.data.data.book
      })
      //显示评论对话框
      this.commentVisible = true
    },
    //添加评论
    addComment() {
      if (this.textarea.trim() === '') {
        return this.$message.info('请填写发表的内容')
      }
      //从sessionStorage获取已经登录的用户
      var item = JSON.parse(window.sessionStorage.getItem('user'))


      /*封装好需要提交到后端的评论的数据*/
      this.addCommentForm.user_id = item.user_id
      this.addCommentForm.user_name = item.username
      this.addCommentForm.comment_info = this.textarea.trim()
      this.addCommentForm.book_id = this.book_id
      this.addCommentForm.book_name = this.book.book_name
      this.addCommentForm.user_img = item.user_img

      this.$http.post('/user/addComment', this.addCommentForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('评论失败')
        }
        this.$message.success('评论成功')
        this.textarea = ''
        this.commentVisible = false
      })
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
    //用户确定收货
    receiving(order_id) {
      this.$http.get('/user/receiving?id=' + order_id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('收货失败')
        }
        this.$message.success('收货成功')
        this.getUserOrderList(this.user.user_id)
      })
    }
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
.name-a > span {
  float: left;
  width: 220px;
  height: 92px;
  overflow: hidden;
  text-overflow: ellipsis;
  /* 弹性伸缩盒子模型显示 */
  display: -webkit-box;
  /* 限制在一个块元素显示的文本的行数 */
  -webkit-line-clamp: 4;
  /* 设置或检索伸缩盒对象的子元素的排列方式 */
  -webkit-box-orient: vertical;
}

.name-a > img {
  float: left;
}

