<template>
  <div id="nav">
    <vue-particles
      color="#555"
      :particleOpacity="0.7"
      :particlesNumber="150"
      shapeType="circle"
      :particleSize="4"
      linesColor="#555"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="false"
      clickMode="push"
      class="lizi"
    />

    <div id="login-container">
      <div style="text-align: center;height: 50px;margin-left: 11%">
        欢迎登录
      </div>
      <el-form status-icon :model="loginForm" :rules="loginFormRules" ref="loginFormRef" label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="账号" prop="username">
          <el-input type="text" autocomplete="off" v-model="loginForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" autocomplete="off" v-model="loginForm.password"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="vercode">
          <el-input type="password" autocomplete="off" v-model="loginForm.vercode" style="width: 165px"
                    @keyup.enter.native="login"></el-input>
          <div style="float: right">
            <img :src="imageUrl" @click="change" style="width: 130px;height: 40px;">
          </div>
        </el-form-item>
        <el-form-item style="margin-top: -20px">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button @click="resetLoginForm">重置</el-button>
          <el-link type="primary" @click="returnhome" style="margin-left: 10px">返回首页</el-link>
          <el-link type="primary" style="margin-left: 10px" @click="goRegister">去注册</el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>

export default {
  name: "login",
  data() {
    return {
      //这是登录表单的数据绑定对象
      loginForm: {
        username: '',
        password: '',
        //验证码
        vercode: ''
      },
      //这是表单的验证规则对象
      loginFormRules: {
        username: [
          {
            required: true,//必填
            message: '请输入用户名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 10,
            message: '长度在 2 到 10 个字符',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 11,
            message: '长度在 6 到 11 个字符',
            trigger: 'blur'
          }
        ],
        //验证码的验证规则
        vercode: [
          {
            required: true,
            message: '请输入验证码',
            trigger: 'blur'
          }
        ]
      },
      //验证码图片地址
      /*imageUrl: 'http://localhost:8080/bookstore_SSM_Vue/getVerifyCode'*/
      imageUrl: 'http://localhost:8080/getVerifyCode'
    }
  },
  methods: {
    //返回首页
    returnhome() {
      this.$router.push('/home')
    },
    //登录
    login() {
      //点击登录的时候先调用validate方法验证表单内容是否有误
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return

        await this.$http.get('/user/userLogin', {params: this.loginForm}).then(res => {
          console.log(res)
          if (res.data.data.vercodeError === "验证码错误") {
            //刷新验证码
            this.change()
            //将验证码文本清空
            this.loginForm.vercode = ''
            return this.$message.error('验证码错误')
          } else if (res.data.data.loginNo === "查无此人") {
            return this.$message.error('用户名或密码错误')
            //刷新验证码
            this.change()
          }

          //登录成功以后将这个用户保存到客服端的sessionStorage中,将它转化为JSON格式的字符串进行保存
          window.sessionStorage.setItem('user', JSON.stringify(res.data.data.loginYes))

          this.$message.success('登录成功')

          //跳转到首页
          this.$router.go(-1);

          /*var item = window.sessionStorage.getItem('user');
          console.log(JSON.parse(item))*/
        })
      })
    },
    //点击重置按钮，重置登录表单
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields()
    },
    //刷新验证码
    change() {
      let num = Math.ceil(Math.random() * 10);//生成一个随机数（防止缓存）
      this.imageUrl = 'http://localhost:8080/getVerifyCode?hyhh=' + num
    },
    //去注册页面
    goRegister(){
      this.$router.push('/userregister')
    }
  }
}
</script>

<style scoped>
body {
  margin: 0;
}

#login-container {
  width: 400px;
  height: 290px;
  background: #e5e9f2;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -220px;
  margin-top: -170px;
  border-radius: 5px;
  padding-top: 40px;
  padding-right: 40px;
}

body {
  width: 100%;
  background-color: rgb(239, 239, 239);
}

.lizi {
  position: fixed;
  top: 0;
  width: 100%;
}

#particles-js {
  width: 100%;
}

</style>
