<template>
  <div>
    <!--首页头部部分-->
    <div>
      <div id="hd">
        <!--头部的上部分-->
        <div id="tools">
          <div class="tools">
            <div class="operate">
              <div class="operate_nav">
                <a @click="goAdminLogin">管理员登录</a>
              </div>
              <!--如果用户没有登录显示-->
              <div class="welcome" v-if="isLogin">
                <a class="login" @click="userlogin">登录</a>
                <a class="register" @click="Register">免费注册</a>
              </div>

              <!--如果用户登录了显示-->
              <el-dropdown v-else class="el-dropdown">
                  <span class="el-dropdown-link">
                    {{user.username}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                    <el-avatar size="medium" :src="user.user_img"></el-avatar>
                  </el-dropdown-item>
                  <a class="loginandout" @click="goUserFeedback">反馈信息</a>
                  <a class="loginandout" @click="goUserInfo">个人信息</a>
                  <a class="loginandout" @click="goCollection">我的收藏</a>
                  <a class="loginandout" @click="logOut" >退出登录</a>
                </el-dropdown-menu>
              </el-dropdown>
              <div class="topmenu_left">欢迎光临小郭书舍</div>
            </div>
          </div>
        </div>
        <!--头部的中间-->
        <div class="logo_line_out">
          <div class="logo_line">
            <a class="text_logo" href="/">
              <img src="../../assets/images/logo.png">
            </a>
            <el-input placeholder="请输入搜索图书名称" v-model="book_keyword" class="input-with-select"
                      @keyup.enter.native="goBookList">
              <el-button slot="append" icon="el-icon-search" @click="goBookList"></el-button>
            </el-input>
            <el-button type="danger" plain class="custom-btn" @click="goCart">购物车</el-button>
            <el-button type="primary" plain class="custom-btn" @click="goMyOrder">我的订单</el-button>
          </div>
        </div>
        <!--头部的下部分-->
        <div class="sub">
          <ul>
            <li>
              <a href="#">热门图书类别</a>
            </li>
            <li v-for="item in thirteenCategory" :key="item.category_id">
              <a href="#" @click="goCategoryBookList(item.category_id,item.category_name)">{{ item.category_name }}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <router-view/>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      //搜索的关键字
      book_keyword: "",
      //随机获取13的条类别信息
      thirteenCategory: [],
      //用户
      user:{}
    }
  },
  created() {
    //随机获取13条类别信息
    this.getThirteenCategory();
    this.user=JSON.parse(window.sessionStorage.getItem('user'));
  },
  methods: {
    //随机获取13条类别信息
    getThirteenCategory() {
      this.$http.get('/public/getThirteenCategory').then(res => {
        if (res.data.code !== 200) {
          return this.$message.error("获取13条类别失败")
        }
        this.thirteenCategory = res.data.data
      })
    },
    //将查询的关键字通过路由地址携带到另一个所有图书组件中进行模糊查询
    goBookList() {
      if (this.book_keyword == null || this.book_keyword == '') {
        return this.$message.info('请输入查询信息')
      }
      this.book_keyword = this.book_keyword.trim()//去掉两端空格
      this.$router.push('/bookList?book_keyword=' + this.book_keyword)
      //刷新整个页面
      this.$router.go(0)
    },
    //去类别图书页面，将类别的id通过路由地址携带到另一个组件中进行查询
    goCategoryBookList(category_id, category_name) {
      this.$router.push('/categorybooklist?category_id=' + category_id + "&category_name=" + category_name)
      //刷新整个页面
      this.$router.go(0)
    },
    //去登录页
    userlogin(){
      //跳转到登录页面
      this.$router.push('/userlogin')
    },
    //退出登录
    logOut() {
      console.log(2222)
      //清除浏览器内存中存的用户
      window.sessionStorage.clear()
      //刷新整个页面
      this.$router.push('/home')
      this.$router.go(0)
    },
    //去我的订单页
    goMyOrder(){
      var user = window.sessionStorage.getItem('user');
      if (user===null){
        return this.$message.info('还未登录')
      }
      this.$router.push('/userorder')
    },
    //去购物车页面
    goCart(){
      var user = window.sessionStorage.getItem('user');
      if (user===null){
        return this.$message.info('还未登录')
      }
      this.$router.push('/cart')
    },
    //去注册页面
    Register(){
      this.$router.push('/userregister')
    },
    //去用户个人详情页
    goUserInfo(){
      this.$router.push('/userinfo')
    },
    //去用户反馈页面
    goUserFeedback(){
      this.$router.push('/userfeedback')
    },
    //去收藏页
    goCollection(){
      this.$router.push('/usercollection')
    },
    //去管理员登录页
    goAdminLogin(){
      this.$router.push('/adminLogin')
    }
  },
  computed: {
    //判断用户登录了没有，登录了就显示退出登录，没有登录就显示登录注册
    isLogin() {
      var user = window.sessionStorage.getItem('user');
      if (user !== null) {
        return false
      }
      return true
    }
  }
}
</script>

<style scoped lang="less">
.el-dropdown{
  cursor: pointer;
}
#hd {
  font: 12px 'Hiragino Sans GB', "simsun", "Arial";
}

#hd .tools {
  width: 100%;
  height: 35px;
  background-color: #F9F9F9;
  border-bottom: 1px solid #f2f2f2;
}

.tools .operate {
  width: 1200px;
  height: 35px;
  margin: 0 auto;
  background-color: #f9f9f9;
}

.tools .operate_nav {
  float: right;
  padding-top: 2px;
  padding-left: 15px;
  width: 80px;
  height: 29px;
}

.operate_nav a {
  display: inline-block;
  width: 75px;
  height: 22px;
  padding-top: 5px;
  line-height: 22px;
  text-align: center;
}

.tools .welcome {
  float: right;
  padding-top: 2px;
  width: 160px;
  height: 29px;
}

.tools .welcome a:hover{
  color: #FF8236;
}

.welcome .login, .register {
  display: inline-block;
  width: 70px;
  height: 22px;
  float: left;
  padding-top: 5px;
  line-height: 22px;
  text-align: center;
}

.tools .operate .el-dropdown{
  float: right;
  padding-top: 11px;
  margin-right: 10px;
}

.loginandout{
  font-size: 14px;
  display: block;
  text-align: center;
  margin-top: 5px;
}

.loginandout:hover{
  color: #FF8236;
}

.operate .topmenu_left {
  line-height: 35px;
}

.logo_line_out .logo_line {
  width: 1200px;
  height: 80px;
  margin: 0 auto;
  /*background-color: red;*/
  position: relative;
}

.text_logo {
  float: left;
  width: 218.18px;
  height: 80px;

}

.text_logo img {
  height: 80px;
  width: 218.18px;
}


.logo_line .input-with-select {
  position: absolute;
  width: 394px;
  left: 395px;
  margin-top: 20px;
}

.logo_line .custom-btn {
  margin-top: 20px;
  float: right;
  width: 100px;
  margin-right: 5px;
}

.sub {
  background: #f9f9f9;
  float: none;
  width: auto;
}

.sub ul {
  width: 1200px;
  margin: 0 auto;
  height: 31px;
  overflow: hidden;
  line-height: 31px;
  padding-left: 0px;

}

.sub ul li {
  display: inline-block;
  float: left;
  padding: 0 15px;
  white-space: nowrap;
}

.sub ul li a {
  color: #646464;
  text-decoration: none;
}

.sub ul li a:hover{
  color: #FFB588;
}
</style>
