<!--用户反馈管理-->
<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户反馈</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-button type="primary" round @click="oneKeyRead">一键读取</el-button>
      <el-table :data="Feedback" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="user_name" label="反馈人用户名"></el-table-column>
        <el-table-column prop="feedback_time" label="反馈时间">
          <template slot-scope="scope">
            {{ scope.row.feedback_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="feedback_info" label="反馈内容"></el-table-column>
        <el-table-column prop="feedback_read" label="是否已读">
          <template slot-scope="scope">
            <span v-if="scope.row.feedback_read===0">未读</span>
            <span v-else>已读</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150px">
          <template slot-scope="scope">
            <!--删除按钮-->
            <el-tooltip effect="dark" content="删除此反馈信息" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeFeedbackById(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="已读" placement="top" :enterable="false">
              <el-button type="success" icon="el-icon-check" circle size="small"
                         @click="updataReadById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页区-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 12]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data() {
    return {
      //所有用户反馈的信息
      Feedback: [],
      //查询反馈的信息
      queryInfo: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 10
      },
      //总条数
      total: 0,
    }
  },
  created() {
    //获取所有用户反馈信息
    this.getFeedbackInfoList()
  },
  methods: {
    //获取所有用户反馈信息
    getFeedbackInfoList() {
      this.$http.get('/admin/getFeedbackInfoList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取所有用户反馈信息失败')
        }
        this.Feedback = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //删除反馈信息
    async removeFeedbackById(id) {
      const confirmResult = await this.$confirm('是否删除此反馈信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/user/removeFeedbackById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除失败')
        }
        this.$message.success('删除反馈信息成功')
        this.getFeedbackInfoList()
      })
    },
    //根据id修改此反馈信息为已读
    updataReadById(id) {
      this.$http.post('/admin/updataReadById?id=' + id).then(res => {
        console.log(res)
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('已读失败')
        }
        this.$message.success('已读此反馈信息')
        this.getFeedbackInfoList()
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getFeedbackInfoList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getFeedbackInfoList()
    },
    //一键读取反馈信息
    async oneKeyRead() {
      const confirmResult = await this.$confirm('确定一键读取所有反馈信息？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消')
      }
      this.$http.post('/admin/oneKeyRead').then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('一键读取反馈信息失败')
        }
        this.$message.success('一键读取反馈信息成功')
        this.getFeedbackInfoList()
      })

    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-table {
  margin-top: 10px;
}
</style>
