<template>
  <div>
    <div class="home clearfix">
      <a href="/">返回首页</a>
      <a @click="previousPage">返回上一页</a>
    </div>

    <!--图书详情区-->
    <div class="info_box clearfix">
      <!--图片区-->
      <div class="info_img_box">
        <div class="info_img_small">
          <img :src="book.book_img">
        </div>
      </div>
      <!--图书详情区-->
      <div class="info_book">
        <h3 class="info_title">
          {{ book.book_name }}
        </h3>

        <div class="info_price">
          <p>
            作者：{{ book.author }}
          </p>
          <p>
            图书类别：{{ category.category_name }}
          </p>
          <p>
            出版社：{{ book.book_man }}
          </p>
          <p>
            现有库存：{{ book.total }}本
          </p>
          <p>
            上架时间：{{ book.add_time|dateFormat1 }}
          </p>
          <p>
            销量：{{ book.sales }}本
          </p>
          <p class="introduction">
            图书简介：{{ book.introduction }}
          </p>
          <p class="price-p">
            价格：¥ {{ book.price }}
          </p>
        </div>

        <el-form inline>
          <!--当商品足够时的按钮-->
          <el-form-item>
            <el-input placeholder="输入购买数量" v-model="harvestInfo.buy_num" type="number" :min="1" :max="book.total"></el-input>
          </el-form-item>
          <el-form-item v-if="book.total>0">
            <el-button type="danger" @click="addShoppingCart">加入购物车</el-button>
            <el-button type="primary" @click="harvestInDialog">立即购买</el-button>
            <el-button type="success" @click="collection">收藏</el-button>
          </el-form-item>

          <!--当上商品不足时的按钮-->
          <el-form-item v-if="book.total<=0">
            <el-button type="danger" disabled>加入购物车</el-button>
            <el-button type="primary" disabled>
              立即购买
            </el-button>
            <el-button type="success" disabled @click="collection">收藏</el-button>
            <el-button type="primary" round disabled>返回</el-button>
          </el-form-item>
        </el-form>

      </div>
    </div>

    <div class="bookcomment">
      <div class="commenttitle">
        <h3>书籍评论</h3>
      </div>

      <div class="textarea clearfix">
        <el-input type="textarea" placeholder="留下点什么吧"
                  v-model="textarea" maxlength="30" show-word-limit :disabled="islogin1">
        </el-input>
        <el-button type="primary" @click="addComment">发表评论</el-button>
      </div>
      <!--评论内容区-->
      <!--<template>
        <el-table :data="bookComment" style="width: 100%">
          <el-table-column prop="user_img" label="头像" width="180">
            <template slot-scope="scope">
              <el-avatar :size="50" :src="scope.row.user_img"></el-avatar>
            </template>
          </el-table-column>
          <el-table-column prop="user_name" label="评论人" width="180"></el-table-column>
          <el-table-column prop="comment_time" label="评论时间" width="180" sortable>
            <template slot-scope="scope">
                <span>
                  {{ scope.row.comment_time|dateFormat1 }}
                </span>
            </template>
          </el-table-column>
          <el-table-column prop="comment_info" label="评论内容"></el-table-column>
          <el-table-column label="操作" v-if="islogin" width="180">
            <template slot-scope="scope">
              &lt;!&ndash;只可以删除用户自己的评论&ndash;&gt;
              <el-button v-if="scope.row.user_id===user.user_id"
                         type="danger" icon="el-icon-delete" circle
                         @click="deleteCommentById(scope.row.id)"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作" v-else width="180">
          </el-table-column>
        </el-table>
      </template>-->

      <div class="commentarea" v-for="(item,i) in bookComment" :key="item.id">
        <div class="comment-hd clearfix">
          <img :src="item.user_img">
          <div class="commentinfo">
            {{ item.user_name }}<br>
            {{i+1}}楼   {{ item.comment_time|dateFormat1 }}
          </div>
          <div class="commentoperation" v-if="islogin">
            <el-button v-if="item.user_id===user.user_id"
                       type="danger" icon="el-icon-delete" circle
                       @click="deleteCommentById(item.id)"></el-button>
          </div>
        </div>
        <div class="comment-bd">
          <p>{{item.comment_info}}</p>
        </div>
      </div>

      <!--分页区-->
      <el-pagination style="text-align: center"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="commentInfo.pagenum"
                     :page-sizes="[1, 2, 4, 5]"
                     :page-size="commentInfo.pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </div>

    <!--收货信息对话框-->
    <el-dialog title="收货信息" :visible.sync="harvestInfoVisible" width="30%" @close="harvestInfoClose">
      <el-form :model="harvestInfo" :rules="harvestInfoRrules" ref="harvestInfoRuleForm">
        <el-form-item label="收货人姓名" prop="nick_name">
          <el-input v-model="harvestInfo.nick_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="收货人电话" prop="tel">
          <el-input v-model="harvestInfo.tel" autocomplete="off" type="number"></el-input>
        </el-form-item>
        <el-form-item label="收货地址" prop="address">
          <el-input v-model="harvestInfo.address" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="harvestInfoVisible = false">取 消</el-button>
        <el-button type="primary" @click="buyNow">确 定</el-button>
      </div>
    </el-dialog>

    <HomeFooter></HomeFooter>
  </div>

</template>

<script>
import HomeFooter from "./HomeFooter";
import BookList from "./BookList";

export default {
  name: "BookInfo",
  data() {
    //验证手机号的规则
    var checkMobile = (rule, value, cd) => {
      //验证手机号的正则表达式
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //合法电话
        return cd()
      }
      cd(new Error('请输入正确的电话号'))
    }
    return {
      //图书详情
      book: {},
      //图书类别
      category: {},
      //保存图书的id
      book_id: '',
      //立即购买信息详情
      harvestInfo: {
        //收货人姓名
        nick_name: '',
        //收货人电话
        tel: 0,
        //收获地址
        address: '',
        //用户的id
        user_id: '',
        //图书的id
        book_id: '',
        //图书的名称
        book_name: '',
        //图书的单价
        buy_price: '',
        //图书的图片
        book_img: '',
        //用户购买的数量
        buy_num: 1,
      },
      //用户评论的内容
      textarea: '',
      //图书评论列表
      bookComment: [],
      //根据图书id查询评论的条件
      commentInfo: {
        book_id: '',
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5,
      },
      //评论的总条数
      total: 0,
      //添加评论的数据对象，需要传给后端
      addCommentForm: {
        user_id: '',
        user_name: '',
        comment_info: '',
        book_id: '',
        book_name: '',
        user_img: ''
      },
      //用户收藏的数据对象，需要传给后端
      userCollectionForm: {
        user_id: '',
        book_id: '',
        book_name: '',
        book_img: ''
      },
      //用户
      user: {},
      //控制收货信息对话框显示与隐藏
      harvestInfoVisible: false,
      //收获信息的验证规则
      harvestInfoRrules: {
        nick_name: [
          {required: true, message: '请输入收货人姓名', trigger: 'blur'},
          {min: 2, max: 5, message: '长度在 2 到 4 个字符', trigger: 'blur'}
        ],
        tel: [
          {
            required: true,//必填
            message: '请输入电话号码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkMobile,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        address: [
          {
            required: true,//必填
            message: '请输入地址',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {min: 8, max: 18, message: '长度在 8 到 18 个字符', trigger: 'blur'}
        ]
      },
    }
  },
  created() {
    //据图书id查询图书详情
    this.getBookDetails(this.$route.query.book_id)
    //保存图书的id
    this.book_id = this.$route.query.book_id
    //根据图书id查询它的所有评论
    this.getCommentList()
    //从sessionStorage取出已经登录的用户
    this.user = JSON.parse(window.sessionStorage.getItem('user'))
    //将用户的地址，电话，姓名放到收货对话框中，以便用户修改
    this.userReceivingInfo()

  },
  methods: {
    //将用户的地址，电话，姓名放到收货对话框中，以便用户修改
    userReceivingInfo() {
      if (this.user == '' || this.user == null) {
        return
      }
      this.harvestInfo.address = this.user.address
      this.harvestInfo.tel = this.user.phone
      this.harvestInfo.nick_name = this.user.username
    },

    //根据图书id查询图书详情
    getBookDetails(book_id) {
      if (this.$route.query.book_id == '' || this.$route.query.book_id == undefined) {
        this.$router.push('/')
      }

      console.log(book_id)
      this.$http.get('/public/getBookDetails', {params: {book_id}}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取图书详情失败')
        }

        this.book = res.data.data.book
        this.category = res.data.data.category
      })
    },
    //根据图书id查询它的所有评论
    getCommentList() {
      //获取路由传递过来的图书id
      this.commentInfo.book_id = this.$route.query.book_id
      this.$http.get('/user/getCommentList', {params: this.commentInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取图书评论信息失败')
        }
        this.bookComment = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.commentInfo.pagesize = newSize
      this.getCommentList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.commentInfo.pagenum = newPage
      this.getCommentList()
    },
    //返回上一页
    previousPage() {
      this.$router.go(-1);
    },
    //添加评论
    addComment() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return this.$message.info('您还未登录')
      }

      if (this.textarea.trim() === '') {
        return this.$message.info('请填写发表的内容')
      }
      this.addCommentForm.user_id = this.user.user_id
      this.addCommentForm.user_name = this.user.username
      this.addCommentForm.comment_info = this.textarea.trim()
      this.addCommentForm.book_id = this.book_id
      this.addCommentForm.book_name = this.book.book_name
      this.addCommentForm.user_img = this.user.user_img

      this.$http.post('/user/addComment', this.addCommentForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.info('评论失败')
        }
        this.$message.success('评论成功')
        this.textarea = ''
        this.getCommentList()
      })
    },
    //根据id删除评论
    async deleteCommentById(id) {
      const confirmResult = await this.$confirm('是否删除该评论?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }
      this.$http.post('/user/deleteCommentById?id=' + id).then(res => {
        if (res.data.code !== 200 || res.data.data <= 0) {
          return this.$message.error('删除评论失败')
        }
        this.$message.success('删除评论成功')
        this.getCommentList()
      })

    },
    //用户收藏
    collection() {
      if (this.user == null || this.user == '') {
        return this.$message.info('您还未登录')
      }
      this.userCollectionForm.user_id = this.user.user_id
      this.userCollectionForm.book_id = this.book_id
      this.userCollectionForm.book_name = this.book.book_name
      this.userCollectionForm.book_img = this.book.book_img

      this.$http.post('/user/collectionBook', this.userCollectionForm, {emulateJSON: true}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('添加收藏失败')
        }
        if (res.data.data.collectionNo === "collectionNo") {
          return this.$message.error('您已添加收藏，请勿重复添加')
        }
        if (res.data.data.collectionYes === "collectionYes") {
          return this.$message.success('添加收藏成功')
        }
      })
    },
    //显示收货信息对话框
    harvestInDialog() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return this.$message.info('您还未登录')
      }
      //显示收货信息对话框
      this.harvestInfoVisible = true
    },
    //立即购买
    buyNow() {
      this.harvestInfo.user_id = this.user.user_id
      this.harvestInfo.book_id = this.book.book_id
      this.harvestInfo.book_name = this.book.book_name
      this.harvestInfo.buy_price = this.book.price
      this.harvestInfo.book_img = this.book.book_img

      if (this.harvestInfo.buy_num === '' || this.harvestInfo.buy_num <= 0) {
        return this.$message.error('购买数量不能为空,并且不能小于零')
      }

      if (this.harvestInfo.buy_num > this.book.total) {
        return this.$message.info('对不起库存不足')
      }

      this.$refs.harvestInfoRuleForm.validate(async valid => {
        if (!valid) return
        await this.$http.post('/user/addShoppingCart', this.harvestInfo, {emulateJSON: true}).then(res => {
          if (res.data.code !== 200 || res.data.data !== 1) {
            return this.$message.error('购买商品失败')
          }
          this.$message.success('购买成功')
          this.harvestInfo.buy_num = 1
          this.harvestInfoVisible = false
          //重新获取数据
          this.getBookDetails(this.book_id)
        })

      })
    },
    //重置表单验证
    harvestInfoClose() {
      this.$refs.harvestInfoRuleForm.resetFields()
    },
    //加入购物车
    addShoppingCart() {
      var user = window.sessionStorage.getItem('user');
      if (user === null) {
        return this.$message.info('还未登录')
      }

      /*if (this.harvestInfo.buy_num === '' || this.harvestInfo.buy_num <= 0) {
        return this.$message.error('加入购物车数量不能为空,并且不能小于零')
      }*/

      if (this.harvestInfo.buy_num > this.book.total) {
        return this.$message.info('库存不足')
      }

      this.$http.post('/user/addCart', {
        user_id: this.user.user_id,
        book_id: this.book_id,
        book_name: this.book.book_name,
        book_img: this.book.book_img,
        price: this.book.price,
        num: this.harvestInfo.buy_num,
      }).then(res => {
        if (res.data.code !== 200 || res.data.data.data <= 0) {
          return this.$message.error('加入购物车失败')
        }
        this.$message.success('加入购物成功，请到购物车中查看')
        this.harvestInfo.buy_num = 1
      })
    }
  },
  computed: {
    //判断用户是否登录了,登录了就显示删除按钮，没有就不显示
    islogin() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item !== null) {
        return true
      }
      return false
    },
    //判断用户是否登录了，登录了就可以输入评论内容，没有就不能输入
    islogin1() {
      var item = JSON.parse(window.sessionStorage.getItem('user'))
      if (item === null) {
        return true
      }
      return false
    },
  },
  components: {
    HomeFooter
  }
}
</script>

<style scoped>
