  width: 800px;
  margin: 7px 0 0 86px;
  /*1.先强制一行内显示文本*/
  white-space:nowrap; (默认normal自动换行)
  /*2.超出的部分隐藏*/
overflow:hidden;
  /*3.文字用省略号代替超出的部分*/
  text-overflow:ellipsis;
}

