import Vue from 'vue'
import Router from 'vue-router'
import Home from "../components/home/Home";
import HomeSubject from "../components/home/HomeSubject";
import BookList from "../components/home/BookList";
import CategoryBookList from "../components/home/CategoryBookList";
import UserLogin from "../components/user/UserLogin";
import BookInfo from "../components/home/BookInfo";
import UserOrder from "../components/user/UserOrder";
import Cart from "../components/user/Cart";
import UserRegister from "../components/user/UserRegister";
import UserInfo from "../components/user/UserInfo";
import UserFeedback from "../components/user/UserFeedback";
import UserCollection from "../components/user/UserCollection";
import adminLogin from "../components/administrators/adminLogin";
import adminHome from "../components/administrators/adminHome";
import adminUsersList from "../components/administrators/user/adminUsersList";
import adminUserFeedback from "../components/administrators/user/adminUserFeedback";
import adminUserComment from "../components/administrators/user/adminUserComment";
import adminRotationChart from "../components/administrators/rotationchart/adminRotationChart";
import adminShippedOrder from "../components/administrators/order/adminShippedOrder";
import adminOrder from "../components/administrators/order/adminOrder";
import adminCategory from "../components/administrators/category/adminCategory";
import adminBook from "../components/administrators/book/adminBook";
import addBook from "../components/administrators/book/addBook";
import MoreBookCategory from "../components/home/MoreBookCategory";

//解决路由跳转原路由或者刷新出错
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(Router)

const routes = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home,
      redirect: '/homesubject',
      children: [
        {path: '/homesubject', component: HomeSubject},
        {path: '/bookList', component: BookList},
        {path: '/categorybooklist', component: CategoryBookList},
        {path: '/bookinfo', component: BookInfo},
        {path: '/userorder', component: UserOrder},
        {path: '/cart', component: Cart},
        {path: '/userinfo', component: UserInfo},
        {path: '/userfeedback', component: UserFeedback},
        {path: '/usercollection', component: UserCollection},
        {path: '/morebookcategory', component: MoreBookCategory},
      ]
    },
    {
      path: '/userlogin',
      component: UserLogin
    },
    {
      path: '/userregister',
      component: UserRegister
    },
    {
      path: '/adminLogin',
      component:adminLogin
    },
    {
      path: '/adminHome',
      component: adminHome,
      redirect: '/adminuserList',
      children:[
        {path:'/adminuserList',component: adminUsersList},
        {path:'/adminusercomment',component: adminUserComment},
        {path:'/adminuserfeedback',component: adminUserFeedback},
        {path:'/adminrotationchart',component: adminRotationChart},
        {path:'/adminbookcategory',component: adminCategory},
        {path:'/adminbook',component: adminBook},
        {path:'/adminbook/goAddBook',component: addBook},
        {path:'/adminorder',component: adminOrder},
        {path:'/adminShippedorder',component: adminShippedOrder},
      ]
    }
  ]
})

export default routes
