<!--用户评论管理-->
<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户评论</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-alert title="如有不当评论请务必删除" type="info" center show-icon></el-alert>
      <!--用户评论区  border是竖线，:stripe是隔行变色-->
      <el-table :data="commentList" :border="true" :stripe="true">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="book_name" label="评论图书的名称"></el-table-column>
        <el-table-column prop="user_name" label="评论人"></el-table-column>
        <el-table-column prop="comment_time" label="评论时间">
          <template slot-scope="scope">
            {{ scope.row.comment_time|dateFormat1 }}
          </template>
        </el-table-column>
        <el-table-column prop="comment_info" label="评论内容"></el-table-column>
        <el-table-column label="操作" width="150px">
          <template slot-scope="scope">
            <!--删除按钮-->
            <el-tooltip effect="dark" content="删除评论" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="small"
                         @click="removeCommentById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //获取用户评论参数对象
      queryInfo: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 10
      },
      //评论列表
      commentList: [],
      //总条数
      total: 0,
    }
  },
  created() {
    //获取所有用户评论
    this.getUserCommentList();
  },
  methods: {
    //获取所有用户评论
    getUserCommentList() {
      this.$http.get('/admin/getUserCommentList', {params: this.queryInfo}).then(res => {
        if (res.data.code !== 200) {
          return this.$message.error('获取所有用户评论失败')
        }
        this.commentList = res.data.data.list
        this.total = res.data.data.total
      })
    },
    //监听分页组件每页显示多少条数据改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserCommentList()
    },
    //监听分页组件页码值改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserCommentList()
    },
    //删除评论
    async removeCommentById(id){
      const confirmResult=await this.$confirm('是否删除此评论?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err=>err)

      //如果用户确认删除，则返回值为字符串confirm,如果用户取消删除，则返回字符串cancel
      if (confirmResult !== 'confirm') {//用户点击了取消
        return this.$message.info('已取消删除')
      }

      this.$http.post('/admin/removeCommentById?id='+id).then(res=>{
        console.log(res)
        if (res.data.code!==200||res.data.data<=0){
          return this.$message.error('删除评论失败')
        }
        this.$message.success('删除成功')
        this.getUserCommentList()
      })
    }
  }
}
</script>

<style scoped>
.el-card {
  margin-top: 10px;
}

.el-pagination {
  width: 50%;
  margin: 0 auto;
}

.el-alert{
  margin-bottom: 10px;
}
</style>
