<template>
  <div id="nav">
    <vue-particles
      color="#555"
      :particleOpacity="0.7"
      :particlesNumber="150"
      shapeType="circle"
      :particleSize="4"
      linesColor="#555"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="false"
      clickMode="push"
      class="lizi"
    />

    <div id="login-container">
      <div style="text-align: center;height: 50px;margin-left: 11%">
        欢迎注册
      </div>
      <el-form status-icon :model="registerForm" :rules="registerFormRules" ref="registerFormRef" label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="用户名" prop="username">
          <el-input type="text" autocomplete="off" v-model="registerForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="registerForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="真实姓名" prop="real_name">
          <el-input autocomplete="off" v-model="registerForm.real_name"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="registerForm.gender">
            <el-radio label="男">男</el-radio>
            <el-radio label="女">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input autocomplete="off" v-model.number="registerForm.age" min="1" max="100"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input autocomplete="off" v-model="registerForm.phone" type="number"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input autocomplete="off" v-model="registerForm.email"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input autocomplete="off" v-model="registerForm.address"></el-input>
        </el-form-item>
        <el-form-item label="邮编" prop="postcode">
          <el-input autocomplete="off" v-model="registerForm.postcode" type="number"></el-input>
        </el-form-item>
        <el-form-item label="注册日期">
          {{ registerForm.register_time|dateFormat1 }}
        </el-form-item>

        <el-form-item style="margin-top: -20px">
          <el-button type="primary" @click="register">注册</el-button>
          <el-button @click="resetRegisterForm">重置</el-button>
          <el-link type="primary" @click="returnhome" style="margin-left: 10px">返回首页</el-link>
          <el-link type="primary" style="margin-left: 10px" @click="goLogin">去登陆</el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    //验证邮箱的规则
    var checkEmail = (rule, value, cd) => {
      //验证邮箱的正则表达式
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        //合法邮箱
        return cd()
      }
      cd(new Error('请输入合法的邮箱'))
    }

    //验证手机号的规则
    var checkMobile = (rule, value, cd) => {
      //验证手机号的正则表达式
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        //合法电话
        return cd()
      }
      cd(new Error('请输入正确的电话号'))
    }
    return {
      //注册信息
      registerForm: {
        username: '',
        password: '',
        real_name: '',
        gender: '男',
        age: '',
        phone: '',
        email: '',
        address: '',
        postcode: '',
        register_time: '',
      },
      //表单验证规则
      registerFormRules: {
        username: [
          {
            required: true,//必填
            message: '请输入用户名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 1,
            max: 8,
            message: '用户名的长度在1~8个字符之间',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,//必填
            message: '请输入密码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 6,
            max: 15,
            message: '用户名的长度在6~15个字符之间',
            trigger: 'blur'
          }
        ],
        real_name: [
          {
            required: true,//必填
            message: '请输入真实姓名',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 2,
            max: 8,
            message: '真实姓名的长度在2~8个字符之间',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,//必填
            message: '请输入电话号码',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkMobile,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        email: [
          {
            required: true,//必填
            message: '请输入邮箱',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            validator: checkEmail,
            trigger: 'blur'//失去焦点的时候触发验证
          }
        ],
        address: [
          {
            required: true,//必填
            message: '请输入地址',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 6,
            max: 15,
            message: '地址的长度在6~15个字符之间',
            trigger: 'blur'
          }
        ],
        postcode: [
          {
            required: true,//必填
            message: '请输入邮编',
            trigger: 'blur'//失去焦点的时候触发验证
          },
          {
            min: 5,
            max: 8,
            message: '邮编的长度在5~8个字符之间',
            trigger: 'blur'
          }
        ],
        //年龄的验证规则
        age: [
          {required: true, message: '年龄不能为空'},
          {type: 'number', message: '年龄必须为数字值'}]
      },


    }
  },
  created() {
    //时间
    this.registerForm.register_time = new Date()
  },
  methods: {
    //注册用户
    register() {
      this.$refs.registerFormRef.validate(valid=>{
        if (!valid) return

        this.$http.get('/user/registerUser',{params:this.registerForm}).then(res=>{
          if (res.data.data.registerNo==="registerNo"){
            return this.$message.error('用户名已被注册')
          }
          if (res.data.data.registerYes==="registerYes"){
            this.$router.push('/home')
            return this.$message.success('注册成功，快去登录吧')
          }
        })
      })
    },
    //点击重置按钮，重置登录表单
    resetRegisterForm() {
      this.$refs.registerFormRef.resetFields()
    },
    //返回首页
    returnhome() {
      this.$router.push('/home')
    },
    //去登录页面
    goLogin() {
      this.$router.push('/userlogin')
    }
  }
}
</script>

<style scoped>
body {
  margin: 0;
}

#login-container {
  width: 400px;
  height: 710px;
  background: #e5e9f2;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -220px;
  margin-top: -375px;
  border-radius: 5px;
  padding-top: 40px;
  padding-right: 40px;
}

body {
  width: 100%;
  background-color: rgb(239, 239, 239);
}

.lizi {
  position: fixed;
  top: 0;
  width: 100%;
}

#particles-js {
  width: 100%;
}
</style>
